
.orderCenter {
  font-size: 0.205rem;
  color: #333;
}
.orderCenter .lists6 {
  height: calc(100vh - 0.682rem - 53px);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.orderCenter .lists6 .list {
  margin-top: 0.17rem;
}
.orderCenter .lists6 .list .time1 {
  background-color: #fff;
  padding: 0.17rem 0;
}
.orderCenter .lists6 .list .time1 span:nth-child(1) {
  margin-left: 0.17rem;
}
.orderCenter .lists6 .list .time1 span:nth-child(2) {
  color: #ef4949;
  float: right;
  margin-right: 0.17rem;
}
.orderCenter .lists6 .list .orderlist {
  width: 100%;
  height: 1.792rem;
  background-color: #f9f9f9;
}
.orderCenter .lists6 .list .orderlist .ul1 li {
  float: left;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(1) {
  width: 1.9rem;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(1) .img1 {
  width: 1.433rem;
  height: 1.433rem;
  background-color: #eee;
  margin-left: 0.25rem;
  margin-top: 0.17rem;
  background-size: cover;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(2) {
  width: calc(100% - 2.15rem);
  height: 1.792rem;
  position: relative;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(2) .name2 {
  font-size: 0.222rem;
  margin-top: 0.17rem;
  max-height: 0.83rem;
  overflow: hidden;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(2) .sku1 {
  margin-top: 0.06rem;
  color: #999;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(2) .price1 {
  font-size: 0.222rem;
  position: absolute;
  bottom: 0.17rem;
  width: 100%;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(1) {
  color: #ef4949;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(2) {
  color: #999;
  text-decoration: line-through;
  margin-left: 0.1rem;
}
.orderCenter .lists6 .list .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(3) {
  color: #999;
  float: right;
}
.orderCenter .lists6 .list .price {
  padding: 0.17rem 0.17rem;
  width: calc(100% - 0.34rem);
  background-color: #fff;
  overflow: auto;
}
.orderCenter .lists6 .list .price span {
  float: right;
}
.orderCenter .lists6 .list .price span:nth-child(1) {
  margin-left: 0.17rem;
}
.orderCenter .lists6 .list .caozuo {
  background-color: #fff;
  padding: 0.25rem 0.17rem;
  width: calc(100% - 0.34rem);
  border-top: 1px solid #eee;
  overflow: auto;
}
.orderCenter .lists6 .list .caozuo span {
  height: 0.384rem;
  width: 1.143rem;
  border-radius: 0.192rem;
  border: 1px solid #999;
  padding: 0.06rem 0.15rem;
  margin-left: 0.17rem;
}
.orderCenter .mint-navbar {
  height: 0.682rem;
}
.orderCenter .mint-navbar .mint-tab-item-label {
  font-size: 0.222rem;
  line-height: 0;
}
.orderCenter .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 2px solid #ef4949;
  color: #ef4949;
  margin-bottom: -2px;
}



