
.teacherList {
  font-size: 0.205rem;
  color: #333;
}
.teacherList .pList {
  background-color: #fff;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem 0.25rem;
  position: relative;
  margin-bottom: 0.17rem;
}
.teacherList .pList .name1 .img1 {
  width: 0.409rem;
  height: 0.409rem;
  border-radius: 50%;
  background-color: #eee;
  margin: 0.17rem 0;
}
.teacherList .pList .name1 span {
  display: inline-block;
  height: 0.749rem;
  vertical-align: top;
  line-height: 0.749rem;
  color: #999;
}
.teacherList .pList .problem1 {
  font-size: 0.222rem;
  margin-bottom: 0.17rem;
  color: #333;
}
.teacherList .pList .yuyin1 {
  width: 3.41rem;
  height: 0.682rem;
  border-radius: 0.341rem;
  position: relative;
}
.teacherList .pList .yuyin1 span {
  color: #fff;
  font-size: 0.239rem;
  display: inline-block;
  vertical-align: top;
  line-height: 0.682rem;
  position: absolute;
  left: 1.1rem;
}
.teacherList .pList .yuyin1 img {
  width: 3.5rem;
  position: absolute;
}
.teacherList .pList .amount1 {
  position: absolute;
  bottom: 0.4rem;
  right: 0.17rem;
  color: #999;
}
.teacherList .choose {
  color: #26a2ff;
}
.teacherList .type {
  background-color: #fff;
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
}
.teacherList .askEd {
  font-size: 0.26rem;
  min-height: auto;
  background-color: #fff;
  width: calc(100% - 0.8rem);
  padding: 0.3rem 0.4rem;
  margin-bottom: 0.2rem;
}
.teacherList .askEd2 {
  font-size: 0.26rem;
  background-color: #fff;
  width: calc(100% - 0.8rem);
  padding: 0.3rem 0.4rem;
  margin-bottom: 0.2rem;
}
.teacherList .wenti {
  font-size: 0.28rem;
  color: #333;
  margin-top: 0.1rem;
}
.teacherList .wenti2 {
  font-size: 0.28rem;
  color: #333;
  margin-top: 0.1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.teacherList .voice {
  height: 0.7rem;
  width: 4rem;
  /*background-color: #fe5875;*/
  display: inline-block;
  border-radius: 0.8rem;
  margin-top: 0.2rem;
  /*text-align: center;*/
  position: relative;
}
.teacherList .voice img {
  width: 3.6rem;
  position: absolute;
}
.teacherList .voice span {
  position: absolute;
  left: 1.2rem;
}
.teacherList .listlenCount {
  position: absolute;
  right: 0;
  line-height: 1.2rem;
  color: #999;
}
.teacherList .voice i {
  position: absolute;
  color: #fff;
  line-height: 0.8rem;
  left: 0.2rem;
}
.teacherList .voice span {
  line-height: 0.7rem;
  color: #fff;
  font-size: 0.26rem;
  display: inline-block;
}
.teacherList .xinxi {
  height: 0.6rem;
  color: #666;
}
.teacherList .xinxi img {
  height: 0.6rem;
  width: 0.6rem;
  border-radius: 0.6rem;
  margin-right: 0.2rem;
}
.teacherList .xinxi ul li {
  float: left;
}
.teacherList .xinxi .name {
  line-height: 0.6rem;
}



