
.recharge {
  height: 100%;
}
.recharge .automon {
  font-size: 0.221rem;
  height: 0.76rem;
  line-height: 0.76rem;
  background-color: #fff;
  padding-left: 0.17rem;
  border-top: 1px solid #eee;
}
.recharge .automon .inputmon {
  width: 1.2rem;
  height: 0.5rem;
  border-radius: 0;
  border: 1px solid #ddd;
  -webkit-appearance: none;
  text-align: center;
  font-size: 0.25rem;
  color: #333;
  float: right;
  margin-right: 0.17rem;
  margin-top: 0.1rem;
}
.recharge .title {
  height: 1.11rem;
  background-color: #fff;
  width: 100%;
  border-bottom: 1px solid #ddd;
}
.recharge .title div {
  font-size: 0.221rem;
  color: #333;
  line-height: 1.11rem;
  margin-left: 0.17rem;
}
.recharge .title div span {
  font-size: 0.29rem;
  color: #fe5875;
  margin-left: 0.17rem;
}
.recharge .content .title1 {
  font-size: 0.221rem;
  color: #999;
  margin: 0.17rem 0 0.17rem 0.17rem;
}
.recharge .content .shu {
  height: 0.76rem;
  background-color: #fff;
  border-top: 1px solid #eee;
}
.recharge .content .shu .zi {
  font-size: 0.221rem;
  line-height: 0.76rem;
  margin-left: 0.17rem;
}
.recharge .content .shu .zi .btn {
  display: inline-block;
  float: right;
  position: relative;
}
.recharge .content .shu .zi .btn i {
  position: absolute;
  right: 0.17rem;
}
.recharge .content .shu .zi .btn .seleted {
  color: #fe5875;
}
.recharge .content .shu .zi .btn input {
  position: absolute;
  right: 0.25rem;
  top: 0.3rem;
  opacity: 0;
}
.recharge .dibu {
  background-color: #fff;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.recharge .dibu .all {
  font-size: 0.221rem;
  margin-left: 0.17rem;
  margin-top: 0.256rem;
  margin-bottom: 0.17rem;
}
.recharge .dibu .all .money {
  font-size: 0.29rem;
  color: #fe5875;
}
.recharge .dibu .pay {
  font-size: 0.221rem;
  width: 6.06rem;
  height: 0.6rem;
  background-color: #21b101;
  border-radius: 0.1rem;
  color: #fff;
  text-align: center;
  margin: 0 auto;
  line-height: 0.6rem;
  margin-bottom: 0.25rem;
}
.recharge .dibu .pay i {
  font-size: 0.26rem;
  margin-right: 2px;
}
.recharge .footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.recharge .mint-tab-container {
  margin-top: 1px;
}
.recharge .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 1px solid #5e96d0;
  color: #5e96d0;
  margin-bottom: -1px;
}



