
.liveHomePage{
	font-size: 0.26rem;
}
.liveHomePage .bgg{
	height: 2.8rem;
}
.liveHomePage .title{
	height: 1.2rem;
	width: 100%;
	background-color: #fff;
	border-bottom: 1px solid #e1e2e6;
}
.liveHomePage .title ul li{
	float: left;
}
.liveHomePage .title ul li img{
	position: relative;
	top: 0.2rem;
	margin-left: 0.2rem;
	margin-right: 0.2rem;
}
.liveHomePage .headPic{
	width: 0.8rem;
	height: 0.8rem;
	border-radius: 0.4rem;
}
.liveHomePage .liveName{
	font-size: 500;
	margin-bottom: 0.05rem;
}
.liveHomePage .newgroup{
	display: inline-block;
    height: 0.9rem;
    width: 3rem;
    background: #3385ff;
    line-height: 0.9rem;
    text-align: center;
    color: #fff;
    border-radius: 0.6rem;
    margin-left: calc(50% - 1.5rem);
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
}
.liveHomePage .edit{
	position: fixed;
	bottom: 0;
	height: 0.8rem;
    width: 100%;
    background: #3385ff;
    line-height: 0.8rem;
    text-align: center;
    color: #fff;
    font-size: 0.3rem;
}
.liveHomePage .mint-tab-item-label{
	font-size: 0.26rem;
}
