
.inviteSort {
  font-size: 0.24rem;
  min-height: 100%;
  background-color: #000;
}
.inviteSort .bg {
  height: 100%;
  width: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  z-index: 2;
  top: -0.24rem;
}
.inviteSort .roleList {
  width: 5.3rem;
  height: 8.6rem;
  background-color: #fffff4;
  border-radius: 0.17rem;
  margin: 0 auto;
}
.inviteSort .roleList .role1 {
  position: relative;
}
.inviteSort .roleList .role1 img {
  width: 1.6rem;
  margin-left: -0.09rem;
}
.inviteSort .roleList .role1 span {
  position: absolute;
  top: 0.02rem;
  left: 0.2rem;
  color: #fff;
}
.inviteSort .roleList .bt {
  width: 3.37rem;
  text-align: center;
}
.inviteSort .role {
  width: 1.1rem;
  height: 0.34rem;
  background-color: #ff414b;
  position: absolute;
  right: 0;
  top: 0.5rem;
  border-radius: 0.34rem 0 0 0.34rem;
  color: #fff;
  text-align: center;
  font-size: 0.205rem;
  line-height: 0.34rem;
}
.inviteSort .sortPic {
  width: 100%;
}
.inviteSort .sortLists {
  width: 96%;
  margin-left: 2%;
  background-color: #fff;
}
.inviteSort .sortLists .list table {
  width: 100%;
  color: #333;
}
.inviteSort .sortLists .list table tr {
  border-bottom: 1px solid #eee;
  height: 1.3rem;
}
.inviteSort .sortLists .list table tr .td1 {
  width: 0.75rem;
  position: relative;
  border-bottom: 1px solid #fff;
}
.inviteSort .sortLists .list table tr .td1 span {
  position: absolute;
  top: 0.35rem;
  left: 0.3rem;
  color: #fff;
}
.inviteSort .sortLists .list table tr .td1 img {
  margin-left: 0.17rem;
  width: 0.41rem;
}
.inviteSort .sortLists .list table tr .td1 .down {
  top: 0.45rem;
}
.inviteSort .sortLists .list table tr .td1 .down2 {
  left: 0.23rem;
  top: 0.45rem;
}
.inviteSort .sortLists .list table tr .td2 {
  width: 1.05rem;
}
.inviteSort .sortLists .list table tr .td2 img {
  width: 0.853rem;
  height: 0.853rem;
  border-radius: 0.1rem;
}
.inviteSort .sortLists .list table tr .td4 span {
  float: right;
  margin-right: 0.17rem;
}



