
.faceList {
  font-size: 0.205rem;
  color: #333;
  width: calc(100% - 0.34rem);
  position: relative;
  margin: 0 auto;
  border: 1px solid #f2f2f2;
}
.faceList .loading1 {
  position: fixed;
  bottom: 0.9rem;
  color: #999;
  left: calc(50% - 0.3rem);
}
.faceList .nodata2 {
  position: fixed;
  bottom: 0.8rem;
  color: #999;
  left: -0.1rem;
}
.faceList .list11 {
  display: inline-block;
  width: 2.944rem;
  background-color: #fff;
  position: absolute;
}
.faceList .list11 .img1 {
  width: 100%;
}
.faceList .list11:nth-child(2n) {
  right: 0;
}
.faceList .list11:nth-child(2n+1) {
  left: 0;
}
.faceList .user1 {
  height: 0.682rem;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  position: relative;
}
.faceList .user1 .img3 {
  width: 0.72rem;
  height: 0.72rem;
  border-radius: 50%;
  position: absolute;
  top: -0.15rem;
  background-color: #fff;
}
.faceList .user1 .img3 .img2 {
  width: 0.68rem;
  height: 0.68rem;
  border-radius: 50%;
  position: absolute;
  top: 0.02rem;
  left: 0.02rem;
}
.faceList .user1 span {
  line-height: 0.682rem;
  margin-left: 0.85rem;
  width: 1.9rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  font-size: 0.222rem;
}
.faceList .detail1 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  overflow: hidden;
  color: #666;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
}
.faceList .zan1 {
  margin-right: 0.17rem;
  float: right;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  color: #ef4949;
}
.faceList .zan1 i {
  font-size: 0.3rem;
}



