
.liveHome{
	font-size: 0.26rem;
}
.liveHome .new{
	height: 1.2rem;
}
.liveHome .new ul li{
	float: left;
}
.liveHome .new .head img{
	height: 0.8rem;
	width: 0.8rem;
	border-radius: 0.8rem;
	margin-top: 0.2rem;
	margin-left: 0.15rem;
	margin-right: 0.1rem;
}
.liveHome .nameCt{
	display: table;
	height: 1.2rem;
}
.liveHome .nameCt .allCt{
	display: table-cell;
	vertical-align: middle;
}
.liveHome .nameCt .allCt .name{
	font-size: 0.24rem;
	color: #666;
	margin-bottom: 0.05rem;
}
.liveHome .nameCt .allCt .content{
	min-height: 0.4rem;
	max-width: 4.36rem;
	background-color: #fff;
	font-size: 0.27rem;
	border-radius: 0.1rem;
	padding: 0.1rem 0.1rem 0.1rem 0.1rem;
	display: inline-block;
	margin-bottom: 0rem;
}



