
#box2 {
  padding-bottom: 0.9rem;
  color: #fff;
}
#main, #main2 {
  /*width: 6rem;*/
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  height: 2rem;
  background-color: #fff;
  /*margin:0 auto;*/
  margin-bottom: 0.15rem;
}
#main div {
  /*flex-basis: 3.5rem*/
}
#main div:nth-of-type(2) {
  -ms-flex-preferred-size: 2.5rem;
      flex-basis: 2.5rem
}
#main2 div {
  -ms-flex-preferred-size: 2.5rem;
      flex-basis: 2.5rem
}
#main2 div:nth-of-type(2) {
  -ms-flex-preferred-size: 3.5rem;
      flex-basis: 3.5rem
}
#main img,
#main2 img {
  height: 2rem;
  width: 3.2rem;
}
#main .titles, #main2 .titles {
  color: #333;
  font-size: 0.24rem;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  width: 2.8rem;
}
#main .author, #main2 .author {
  font-size: 0.21rem;
  color: #999;
  text-align: center;
}
#main .div2, #main2 .div1 {
  /*padding-top: 0.6rem;*/
  padding-right: 0.2rem;
  padding-left: 0.2rem;
  height: 2rem;
  width: 2.3rem;
  display: table;
}
#main .author, #main2 .author {
  margin-top: 0.1rem;
}
.middle {
  display: table-cell;
  vertical-align: middle;
}



