
.applyLive{
	font-size: 0.26rem;
	color: #333;
}
.applyLive .row{
	height: 1rem;
	width: calc(100% - 0.4rem);
	background: #fff;
	line-height: 1rem;
	padding: 0 0.2rem;
	border-top: 1px solid #f1f2f6;
}
.applyLive .row:nth-child(1){
	border-top: 0
}
.applyLive .row img{
	width: 0.6rem;
	height: 0.6rem;
	border-radius: 0.3rem;
	position: relative;
	top: 0.1rem;
}
.applyLive .row ul li{
	float: left;
}
.applyLive .row ul li:nth-child(2){
	float: right;
	height: 1rem;
	/*margin-top: 0.1rem;*/
}
.row i{
	color: #eaeaea;
}
.liveDesc input,
.liveName input{
	background: #fff;
	border:0;
	text-align: right;
	font-size: 0.26rem;
	position: relative;
	top: -0.08rem;
}
.applyLive .liveBg{
	background: #fff;
	height: 1.4rem;
	border-top: 1px solid #f1f2f6;
	width: calc(100% - 0.4rem);
	padding: 0 0.2rem;
}
.applyLive .liveBg ul li{
	float: left;
}
.applyLive .liveBg ul li:nth-child(2){
	float: right;
	height: 1.4rem;
}
.applyLive .liveBg ul li:nth-child(2) input{
	background: #fff;
	border:0;
}
.applyLive .liveBg ul li:nth-child(2) i{
	color: #e1e2e6;
}
.liveBg img{
	width: 0.8rem;
	height: 0.8rem;
}
.applyLive .next{
	background: #3385ff;
	width: 2.4rem;
	height: 0.6rem;
	text-align: center;
	line-height: 0.6rem;
	color: #fff;
	border-radius: 0.3rem;
	margin-left: calc(50% - 1.2rem);
	margin-top: 3rem;
}
.mint-msgbox-title{
	font-size: 0.3rem;
	color: #333;
}
.mint-msgbox-confirm,
.mint-msgbox-cancel{
	font-size: 0.26rem;
}
.mint-msgbox-input input{
	height: 0.4rem;
	font-size: 0.26rem;
	color: #666;
	position: relative;
	left: -0.1rem;
	text-align: center;
}



