
.gifilist {
  font-size: 0.26rem;
  height: 100%;
  overflow: hidden;
}
.gifilist .mint-navbar {
  height: 0.8rem;
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
}
.gifilist .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 2px solid #ff9421;
  color: #ff9421;
  margin-bottom: -2px;
  font-size: 0.26rem;
}
.gifilist .mint-tab-item-label {
  font-size: 0.26rem;
}
.gifilist img {
  width: 1rem;
  height: 1rem;
}
.gifilist ul {
  height: calc(99.2vh);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  margin-top: 0.9rem;
}
.gifilist li {
  background-color: #fff;
  border-top: 1px solid #f1f2f6;
  height: 1.1rem;
  padding-top: 0.05rem;
}
.gifilist li:last-child {
  margin-bottom: 0.9rem;
}
.gifilist .load {
  text-align: center;
}
.gifilist .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 1px solid #5e96d0;
  color: #5e96d0;
  margin-bottom: -1px;
}



