
.commonProblem {
  background-color: #fff;
  overflow: scroll;
  width: 100%;
  font-size: 0.26rem;
  height: 100vh;
  -webkit-overflow-scrolling: touch;
}
.commonProblem-list {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  border-bottom: 1px solid #eee;
}
.commonProblem-list-question li,
.commonProblem-list-answer li {
  float: left;
}
.commonProblem-list-question-right,
.commonProblem-list-answer-right {
  width: 5.2rem;
  overflow: hidden;
}
.commonProblem-list-question {
  overflow: auto;
  margin-bottom: 0.2rem;
  margin-top: 0.3rem;
}
.commonProblem-list-answer {
  color: #999;
  overflow: auto;
  padding-bottom: 0.3rem;
}



