
.commentComplete {
  font-size: 0.205rem;
  color: #000;
}
.commentComplete .main {
  margin-bottom: 0.2rem;
  padding: 0 0.2rem;
  background-color: #ffffff;
}
.commentComplete .main .table {
  display: table;
  width: 100%;
  padding: 0.3rem 0 0.35rem;
}
.commentComplete .main .table .table_cell {
  display: table-cell;
  vertical-align: middle;
}
.commentComplete .main .table .table_cell:first-of-type {
  width: 1rem;
  padding-right: 0.2rem;
}
.commentComplete .main .table .table_cell .main_img {
  width: 1rem;
  height: 1rem;
}
.commentComplete .main .table .table_cell .name {
  font-size: 0.26rem;
  color: #333;
}
.commentComplete .main .table .table_cell .type {
  margin-top: 0.1rem;
  font-size: 0.24rem;
  color: #999;
}
.commentComplete .main .score {
  font-size: 0;
  line-height: 1;
  color: #333;
  padding-bottom: 0.2rem;
  border-bottom: 0.02rem solid #eeeeee;
}
.commentComplete .main .score > span {
  font-size: 0.24rem;
  margin-right: 0.1rem;
}
.commentComplete .main .score .star {
  display: inline-block;
  vertical-align: -0.04rem;
  font-size: 0;
}
.commentComplete .main .score .star span {
  display: inline-block;
  width: 0.45rem;
  height: 0.5rem;
}
.commentComplete .main .score .star .icon-start {
  color: #ef4949;
  font-size: 0.42rem;
}
.commentComplete .main .score .star .icon-xingxing_kongxin {
  font-size: 0.42rem;
}
.commentComplete .main .comment .commenttext {
  width: 100%;
  height: 1.5rem;
  padding-top: 0.2rem;
  font-size: 0.24rem;
  resize: none;
  border: none;
}
.commentComplete .main .comment .commenttext:focus {
  outline: none;
  color: #333333;
}
.commentComplete .main .comment .commenttext:-moz-placeholder {
  color: #cccccc;
}
.commentComplete .main .comment .commenttext:-ms-input-placeholder {
  color: #cccccc;
}
.commentComplete .main .comment .commenttext::-webkit-input-placeholder {
  color: #cccccc;
}
.commentComplete .main .comment .upload {
  padding-bottom: 0.2rem;
}
.commentComplete .main .comment .upload .icon-tianjiatupian {
  font-size: 0.7rem;
  color: #999999;
}
.commentComplete .main .upload {
  font-size: 0;
}
.commentComplete .main .upload span {
  display: inline-block;
  position: relative;
  width: 0.7rem;
  height: 0.7rem;
  vertical-align: middle;
  margin-right: 0.3rem;
}
.commentComplete .main .upload span:last-of-type {
  margin-right: 0;
}
.commentComplete .main .upload span img {
  width: 0.7rem;
  height: 0.7rem;
}
.commentComplete .main .upload .uploadfile {
  position: absolute;
  left: 0;
  top: 0;
  width: 0.7rem;
  height: 0.7rem;
  opacity: 0;
}
.commentComplete .anonymousWrap {
  padding: 0 0.2rem;
  background-color: #ffffff;
}
.commentComplete .anonymousWrap .anonymous {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.24rem;
  border-top: 0.02rem solid #eeeeee;
  background-color: #ffffff;
}
.commentComplete .anonymousWrap .anonymous div {
  line-height: 0.8rem;
}
.commentComplete .anonymousWrap .anonymous div:first-of-type {
  color: #333333;
}
.commentComplete .anonymousWrap .anonymous div:first-of-type span {
  padding-right: 0.1rem;
  vertical-align: middle;
}
.commentComplete .anonymousWrap .anonymous div:first-of-type span .icon-check {
  color: #999;
}
.commentComplete .anonymousWrap .anonymous div:first-of-type span .red {
  color: #ef4949;
}
.commentComplete .anonymousWrap .anonymous div:last-of-type {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: right;
  color: #999999;
}
.commentComplete .btn {
  margin: 0.3rem 0.2rem 0;
  line-height: 0.8rem;
  text-align: center;
  font-size: 0.3rem;
  color: #fff;
  background-color: #ef4949;
}



