
.goodsLists {
  font-size: 0.205rem;
}
.goodsLists .search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  padding: 0.17rem 0;
  border-bottom: 1px solid #eee;
  background-color: #fff;
  height: 0.44rem;
}
.goodsLists .search .i1 {
  font-size: 0.3rem;
  line-height: 0.5rem;
  margin-left: 0.17rem;
  margin-right: 0.17rem;
}
.goodsLists .search .i2 {
  position: absolute;
  color: #ccc;
  font-size: 0.35rem;
  top: 0.05rem;
  left: 0.05rem;
}
.goodsLists .search input {
  height: 0.43rem;
  border-radius: 0.3rem;
  outline-style: none;
  background-color: #eee;
  border: 1px solid #eee;
  text-align: center;
  width: 4.85rem;
  margin-right: 0.17rem;
  -webkit-appearance: none;
}
.goodsLists .search .sousuozi {
  font-size: 0.222rem;
  line-height: 0.44rem;
}
.goodsLists .orderType {
  height: 0.6rem;
  line-height: 0.6rem;
  font-size: 0.222rem;
  background-color: #fff;
  border-bottom: 1px solid #eee;
}
.goodsLists .orderType .ul1 > li {
  float: left;
  width: 33.33333333%;
  text-align: center;
  height: 0.6rem;
}
.goodsLists .orderType .ul2 {
  height: 0.6rem;
  display: inline-table;
  position: relative;
  top: -0.06rem;
}
.goodsLists .orderType .ul2 li {
  height: 0.1rem;
}
.goodsLists .orderType .ul2 li i {
  font-size: 0.1rem;
}
.goodsLists .lists {
  background-color: #fff;
  width: 100%;
  overflow: scroll;
}
.goodsLists .lists ul {
  width: 100%;
  height: calc(100vh - 1.41rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.goodsLists .lists ul li {
  float: left;
  width: 2.9rem;
  height: 3.41rem;
  border: 1px solid #eee;
  background-color: #fff;
  margin-left: 0.17rem;
  margin-top: 0.17rem;
}
.goodsLists .lists ul li .img1 {
  width: 100%;
  height: 2.645rem;
}
.goodsLists .lists ul li .title {
  width: 2.73rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-weight: 500;
  margin: 0.08rem 0 0.05rem 0.085rem;
}
.goodsLists .lists ul li .price {
  margin-left: 0.085rem;
  color: #ef4949;
}
.goodsLists .lists ul li:nth-child(2n) {
  float: right;
  margin-right: 0.17rem;
}



