
.mallTab {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.mallTab .mint-tab-item {
  color: #ccc;
  background-color: #fff;
}
.mallTab .mint-tabbar > .mint-tab-item.is-selected {
  background-color: #fff;
  color: #ef4949;
}
.mallTab .mint-tabbar {
  border-top: 1px solid #ddd;
}
.mallTab .mint-tab-item-label div {
  font-size: 0.205rem;
  margin-top: 0.05rem;
}



