
.lottery{
	width: 100%;
	overflow-x: hidden;
}
.lottery .xinxi img{
	width: 0.5rem;
	border-radius: 100%;
	position: relative;
	top: 0.05rem;
}
.lottery .xinxi{
	background-color: #fff;
	height: auto;
	width: calc(100% - 0.4rem);
	margin: 0rem 0.2rem 0rem 0.2rem;
	text-align: center;
	height: 6rem;
	overflow: scroll;
	-webkit-overflow-scrolling:touch;
}
.lottery .nums{
	text-align: center;
	color: #fff;
}
.lottery{
	font-size: 0.26rem;
	background-color: #E13658;
	width: 100%;
}
.lottery .time{
	font-size: 0.24rem;
	text-align: center;
	color: #fff;
	margin-top: 0.3rem;
	/*
	background-color: #fe633a;
	margin-top: 0.2rem;
	padding: 0.05rem 0.4rem;
	border-radius: 0.5rem;
	display: inline-block;
	margin-left: 0.15rem;*/
}
.lottery #title{
	text-align: center;
	background-color: #fa5023;
	color: #fff;
	font-weight: bold;
	padding: 0.2rem;
	font-size: 0.3rem;
}
.lottery .share{
	width: 100%;
	text-align: center;
	margin-top: 0.1rem;
}
.lottery .share p{
	text-align: center;
	color: #440095;
	font-weight: bold;
	background-color: #fffb00;
	display: inline-block;
	padding: 0.1rem 0.1rem;
}
.lottery .turnplate_box{
	width: 300px;
	height: 300px;
	margin: 10px auto;
	position: relative;
}
.lottery .turnplate_box canvas{
	position: absolute;
}
.lottery #myCanvas{
	background-color: white;
	border-radius: 100%;
}
.lottery #myCanvas01,#myCanvas03{
	left: 50px;
	top: 50px;
	z-index: 30;
}
.lottery #myCanvas02{
	left: 75px;
	top: 75px;
	z-index: 20;
}
.lottery #myCanvas{
    -o-transform:  transform 6s;
	-ms-transform:  transform 6s;
	-moz-transform:  transform 6s;
	-webkit-transform:  transform 6s;
	-webkit-transition: -webkit-transform 6s;
	transition: -webkit-transform 6s;
	transition: transform 6s;
	transition: transform 6s, -webkit-transform 6s;
	-webkit-transform-origin:  50% 50%;
	transform-origin: 50% 50%;
}
.lottery .turnplatw_btn{
	width: 60px;
	height: 60px;
	left: 120px;
	top: 120px;
	border-radius: 100%;
	position: absolute;
	cursor: pointer;
	border: none;
	background: transparent;
	outline: none;
	z-index: 40;
}




