
.facePromotion {
  font-size: 0.205rem;
  color: #333;
  height: 100%;
  overflow: hidden;
  background-color: #333;
}
.facePromotion .box1 {
  overflow-x: hidden;
  white-space: nowrap;
  position: relative;
  top: calc(50% - 4.3rem);
}
.facePromotion .box1 #shareTu1,
.facePromotion .box1 #shareTu2,
.facePromotion .box1 #shareTu3 {
  width: 4.608rem;
}
.facePromotion .box1 .margin1 {
  margin-left: 0.896rem;
  margin-right: 0.4267rem;
}
.facePromotion .box1 .margin2 {
  margin-right: 0.4267rem;
}
.facePromotion .box1 .margin3 {
  margin-right: 0.896rem;
}
.facePromotion .tip {
  position: relative;
  top: calc(50% - 4.1rem);
  color: #fff;
  text-align: center;
}
.facePromotion .tip img {
  width: 0.2rem;
  margin-right: 0.1rem;
}
.facePromotion .leftBg {
  width: 0.41rem;
  height: 8.2rem;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: calc(50% - 4.3rem);
  left: 0;
}
.facePromotion .rightBg {
  width: 0.42rem;
  height: 8.2rem;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: calc(50% - 4.3rem);
  right: 0;
}



