
.serAsk {
  font-size: 0.26rem;
}
.serAsk .choose {
  color: #5e96d0;
  border-bottom: 1px solid #5e96d0;
}
.serAsk .load {
  text-align: center;
}
.serAsk .active {
  color: #5e96d0;
}
.serAsk .type {
  background-color: #fff;
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
}
.serAsk .title {
  height: 0.68rem;
  background-color: #fff;
  margin-top: 1px;
  color: #999;
  font-size: 0.222rem;
}
.serAsk .title span {
  background-color: #fff;
  display: inline-block;
  padding: 0.05rem 0.2rem;
  border-radius: 0.3rem;
  margin-top: 0.13rem;
  margin-left: 0.3rem;
}
.serAsk .type ul li {
  float: left;
  width: 33.33333333%;
  text-align: center;
  color: #999;
}
.serAsk .askEd {
  font-size: 0.26rem;
  /*min-height: 3rem;*/
  min-height: auto;
  background-color: #fff;
  width: calc(99.2%);
  padding: 0.3rem 0.4rem;
}
.serAsk .askEd2 {
  font-size: 0.26rem;
  background-color: #fff;
  width: calc(99.2%);
  padding: 0.3rem 0.4rem;
  margin-bottom: 0.2rem;
}
.serAsk .wenti {
  font-size: 0.28rem;
  color: #000;
  margin-top: 0.1rem;
}
.serAsk .wenti2 {
  font-size: 0.28rem;
  color: #000;
  margin-top: 0.1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.serAsk .voice {
  height: 0.7rem;
  width: 4rem;
  /*background-color: #fe5875;*/
  display: inline-block;
  border-radius: 0.8rem;
  margin-top: 0.2rem;
  /*text-align: center;*/
  position: relative;
}
.serAsk .listlenCount {
  position: absolute;
  right: 0;
  line-height: 1.2rem;
  color: #666;
}
.serAsk .voice i {
  position: absolute;
  color: #fff;
  line-height: 0.8rem;
  left: 0.2rem;
}
.serAsk .voice span {
  line-height: 0.7rem;
  color: #fff;
  font-size: 0.26rem;
  display: inline-block;
}
.serAsk .xinxi {
  height: 0.6rem;
  color: #666;
}
.serAsk .xinxi img {
  height: 0.6rem;
  width: 0.6rem;
  border-radius: 0.6rem;
  margin-right: 0.2rem;
}
.serAsk .xinxi ul li {
  float: left;
}
.serAsk .xinxi .name {
  line-height: 0.6rem;
}



