
.contentPay .lessTip {
  height: 0.5rem;
  width: 100%;
  background: #f7c83b;
  position: fixed;
  bottom: 0.8rem;
  color: #fff;
  text-align: center;
  line-height: 0.5rem;
}
.contentPay .payTitle {
  height: 0.8rem;
  width: 100%;
  text-align: center;
  line-height: 0.8rem;
  background-color: #fff;
  margin-bottom: 0.2rem;
  border-bottom: 1px solid #e1e1e1
}
.contentPay .yihang {
  width: 100%;
  height: 0.7rem;
  background-color: #fff;
  line-height: 0.7rem;
  border-top: 1px solid #e1e1e1;
}
.contentPay .yihang ul li:nth-child(1) {
  padding-left: 0.17rem;
}
.contentPay .yihang ul li:nth-child(2) {
  padding-right: 0.17rem;
  float: right;
}
.contentPay .yihang ul li {
  float: left;
}
.contentPay .fin {
  height: 0.8rem;
  width: 100%;
  background-color: #fff;
  line-height: 0.8rem;
  position: fixed;
  bottom: 0px;
}
.contentPay .fin ul li {
  float: left;
}
.contentPay .fin ul li:nth-child(1) {
  /*padding-left: 0.17rem;*/
  width: 4.4rem;
  line-height: 0.8rem;
  font-weight: 500;
}
.contentPay .fin ul li:nth-child(1) span {
  display: inline-block;
  margin-left: 0.17rem;
}
.contentPay .fin ul li:nth-child(2) {
  width: calc(100% - 4.4rem);
  height: 0.8rem;
  background-color: #3385ff;
  color: #fff;
  text-align: center;
}
.contentPay {
  background-color: #f1f2f6;
  height: 100%;
  font-size: 0.27rem;
  -webkit-overflow-scrolling: touch；
}
.contentPay .title {
  background-color: #fff;
  height: 1.96rem;
  width: 100%;
}
.contentPay .title ul li {
  float: left;
}
.contentPay .title ul li:nth-child(1) {
  height: 1.96rem;
  width: 1.7rem;
}
.contentPay .title ul li:nth-child(1) img {
  width: 1.36rem;
  height: 1.36rem;
  margin-top: 0.3rem;
  margin-left: 0.17rem;
}
.contentPay .title ul li:nth-child(2) {
  width: calc(100% - 1.7rem);
  display: table;
  height: 1.96rem;
}
.contentPay .title ul li .wenzi {
  display: table-cell;
  vertical-align: middle;
  line-height: 0.38rem;
}
.contentPay .biaoti {
  overflow: hidden;
  text-overflow: ellipsis;
  width: 4.6rem;
  color: #333;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.contentPay .yuanjia, .cuxiao {
  color: #ef3230;
  display: inline-block;
  margin-left: 0.1rem;
}
.contentPay .ifApply {
  display: inline-block;
  float: right;
  padding-right: 0.07rem;
}
.contentPay .yue .money {
  line-height: 0.7rem;
  display: inline-block;
  padding-left: 0.17rem;
}
.contentPay .yue {
  height: 0.7rem;
  width: 100%;
  background-color: #fff;
  margin-top: 0.2rem;
}
.contentPay .mint-switch {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  margin-top: 0.05rem;
}
.contentPay .payWay {
  background-color: #fff;
  padding-left: 0.17rem;
  height: 0.55rem;
  line-height: 0.55rem;
  margin-top: 0.2rem;
}
.contentPay .payWay1 {
  height: 0.8rem;
  background-color: #fff;
  border-top: 1px solid #e1e1e1;
}
.contentPay .payWay1 ul li {
  float: left;
}
.contentPay .payWay1 ul li:nth-child(1) {
  padding-left: 0.17rem;
  line-height: 0.8rem;
}
.contentPay .payWay1 ul li:nth-child(1) img {
  height: 0.4rem;
  position: relative;
  top: 0.1rem;
}
.contentPay .payWay1 ul li:nth-child(1) span {
  display: inline-block;
  margin-left: 0.2rem;
}
.contentPay .payWay1 ul li:nth-child(2) {
  float: right;
}
.contentPay .payWay1 ul li:nth-child(2) .radio {
  width: 0.4rem;
  height: 0.4rem;
  background-color: #fff;
  display: inline-block;
  border-radius: 0.4rem;
  margin-top: 0.2rem;
  margin-right: 0.17rem;
  text-align: center;
  line-height: 0.4rem;
  color: #fff;
}



