
.rechargeList {
  font-size: 0.24rem;
}
.rechargeList .nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  background-color: #fff;
  height: 0.8rem;
  line-height: 0.8rem;
}
.rechargeList .nav div {
  width: 50%;
  text-align: center;
  color: #999;
}
.rechargeList .nav .selected {
  color: #5e96d0;
  border-bottom: 1px solid #5e96d0;
}
.rechargeList .item {
  background-color: #fff;
  height: 0.9rem;
  width: 100%;
  margin-top: 0.17rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
}
.rechargeList .item .allTop {
  display: table;
  height: 0.9rem;
  width: 50%;
}
.rechargeList .item .allTop .all {
  display: table-cell;
  vertical-align: middle;
  padding-left: 0.17rem;
}
.rechargeList .item .allTop .all div:nth-child(2) {
  color: #999;
  margin-top: 0.05rem;
}
.rechargeList .item .allTop1 {
  width: 50%;
}
.rechargeList .item .allTop1 .recharge1,
.rechargeList .item .allTop1 .recharge {
  float: right;
  display: inline-block;
  line-height: 0.9rem;
  padding-right: 0.17rem;
}
.rechargeList .item .allTop1 .recharge1 {
  color: red;
}
.rechargeList .xuan {
  height: 0.76rem;
  width: 100%;
  background-color: #fff;
  line-height: 0.76rem;
  color: #999;
}
.rechargeList .xuan span {
  margin-left: 0.17rem;
}
.rechargeList .xuan .selected {
  color: #5e96d0;
}



