
#setCard {
  background-color: #fff;
  font-size: 0.24rem;
  padding-bottom: 0.8rem;
}
#setCard .pic_box:after,
#setCard .phone:after,
#setCard .email:after,
#setCard .pic_box:after,
#setCard .address:after,
#setCard .position:after,
#setCard .details:after,
#setCard .true_name:after {
  display: block;
  content: '';
  clear: both;
}
#setCard .phone,
#setCard .tel,
#setCard .email,
#setCard .true_name {
  margin: 0 0.2rem;
  font-size: 0.24rem;
  height: 0.8rem;
  border-bottom: 1px solid #e1e2e6;
}
#setCard .phone div:first-child,
#setCard .tel div:first-child,
#setCard .email div:first-child,
#setCard .true_name div:first-child {
  width: 1.4rem;
  text-align: left;
  display: inline-block;
  line-height: 0.8rem;
}
#setCard .phone div:last-child,
#setCard .tel div:last-child,
#setCard .email div:last-child,
#setCard .true_name div:last-child {
  display: inline-block;
  float: right;
}
#setCard .phone div:last-child input,
#setCard .tel div:last-child input,
#setCard .email div:last-child input,
#setCard .true_name div:last-child input {
  height: 0.6rem;
  text-align: right;
  font-size: 0.22rem;
  border: none;
  outline-style: none;
  margin-top: 0.1rem;
  color: #999;
  background: none;
}
#setCard .pic_box {
  height: 1.3rem;
  padding: 0.2rem;
  border-bottom: 1px solid #e1e2e6;
}
#setCard .pic_box div:first-child {
  float: left;
  margin-top: 1rem;
}
#setCard .pic_box div:last-child {
  line-height: 2rem;
}
#setCard .pic_box div:last-child img {
  width: 1.3rem;
  height: 1.3rem;
  margin: 0 auto;
  display: block;
}
#setCard .address,
#setCard .position,
#setCard .details {
  border-bottom: 1px solid #e1e2e6;
  height: 1.3rem;
  margin: 0 0.2rem;
}
#setCard .address div:first-child,
#setCard .position div:first-child,
#setCard .details div:first-child {
  float: left;
  margin-top: 0.8rem;
}
#setCard .address div:last-child,
#setCard .position div:last-child,
#setCard .details div:last-child {
  float: right;
  width: 70%;
}
#setCard .address div:last-child textarea,
#setCard .position div:last-child textarea,
#setCard .details div:last-child textarea {
  background: none;
  margin-left: 0.2rem;
  margin-top: 0.2rem;
  width: 90%;
  font-size: 0.22rem;
  color: #999;
  padding: 0.05rem;
  resize: none;
  border-color: #eee;
}
#setCard .btn {
  width: 60%;
  height: 0.8rem;
  background: #007AFF;
  margin: 0 auto;
  border-radius: 0.4rem;
  text-align: center;
  line-height: 0.8rem;
  color: white;
  margin-top: 0.8rem;
}



