
#box11 {
  height: 100%;
}
#box11 .header {
  height: 1.5rem;
  width: 100%;
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  font-size: 0.26rem;
  color: #666;
}
#box11 .header .middle {
  display: table;
  height: 100%;
  width: 33.33333333%;
}
#box11 .header .middle .tablecell {
  display: table-cell;
  vertical-align: middle;
}
#box11 .header .middle .tablecell .ii {
  width: 0.8rem;
  height: 0.8rem;
  margin: 0 auto;
}
#box11 .header .middle .tablecell .ii i {
  font-size: 0.8rem;
  width: 0.8rem;
}
#box11 .header .middle .tablecell .name {
  text-align: center;
  margin-top: 0.1rem;
}
#box11 .wenzahng {
  height: 4.15rem;
  width: 6rem;
  margin: 0 auto;
  background-color: #fff;
  margin-top: 0.2rem;
  border-radius: 0.2rem;
  font-size: 0.28rem;
  color: #5e96d0;
  font-weight: bold;
}
#box11 .wenzahng .tou {
  padding-top: 0.2rem;
  margin-left: 0.2rem;
}
#box11 .wenzahng .tou span:nth-child(2) {
  float: right;
  color: #999;
  font-size: 0.25rem;
}
#box11 .wenzahng .tou span:nth-child(2) i {
  font-size: 0.25rem;
  margin-right: 0.2rem;
}
#box11 .wenzahng .tou .yi {
  margin-top: 0.4rem;
}
#box11 .wenzahng .tou .yi img {
  height: 1.5rem;
  width: 2.05rem;
}
#box11 .wenzahng .tou .yi .xinxi {
  display: inline-block;
  font-weight: normal;
  color: #333;
  position: relative;
  top: -0.45rem;
  margin-left: 0.2rem;
}
#box11 .wenzahng .tou .yi .xinxi .title {
  width: 3.2rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
#box11 .wenzahng .tou .qita {
  font-weight: normal;
  color: #333;
  padding-top: 0.25rem;
  width: 5.5rem;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
#box11 .wenzahng .tou .qita i {
  color: #5e96d0;
  font-size: 0.3rem;
  margin-right: 0.1rem;
  font-weight: 500;
}
#box11 .wenzahng1 .title {
  color: #5e96d0;
  font-size: 0.238rem;
  margin-left: 0.17rem;
  margin-top: 0.17rem;
  position: relative;
  font-weight: bold;
}
#box11 .wenzahng1 .title span {
  float: right;
  position: absolute;
  right: 0.12rem;
  top: -0.1rem;
  color: #999;
}
#box11 .wenzahng1 .title span i:before {
  font-size: 0.25rem;
}
#box11 .wenzahng1 .content {
  width: 6.058rem;
  margin: 0 auto;
  font-size: 0.238rem;
}
#box11 .wenzahng1 .content .list {
  width: 2.944rem;
  height: 2.944rem;
  background-color: #37ace8;
  display: inline-block;
  float: left;
  margin-top: 0.17rem;
  position: relative;
  background-size: cover;
}
#box11 .wenzahng1 .content .list i:before {
  font-size: 0.29rem;
}
#box11 .wenzahng1 .content .list .title1 {
  font-size: 0.238rem;
  position: absolute;
  bottom: 0;
  color: #fff;
  width: 100%;
  /* Safari 5.1 - 6.0 */
  /* Opera 11.1 - 12.0 */
  /* Firefox 3.6 - 15 */
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#000));
  background: linear-gradient(transparent, #000);
  /* 标准的语法（必须放在最后） */
}
#box11 .wenzahng1 .content .list .title1 .chang {
  width: 2.604rem;
  margin-left: 0.17rem;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
#box11 .wenzahng1 .content .list .title1 .fujia {
  padding-left: 0.17rem;
  width: 100%;
  padding-bottom: 0.17rem;
}
#box11 .wenzahng1 .content .list:nth-child(2n) {
  margin-left: 0.17rem;
}
#box11 .wenzahng1 .content .list:last-child {
  margin-bottom: 0.17rem;
}
#box1 .mint-swipe {
  height: 3.413rem;
}
#box1 .mint-navbar {
  background-color: #fff;
  color: #fff;
  overflow: hidden;
  height: 0.8rem;
}
#box1 .mint-navbar .mint-tab-item.is-selected {
  color: #5e96d0;
  border-bottom: 1px solid #5e96d0;
  margin-bottom: 0px;
}
#box1 .mint-navbar {
  top: 0;
  z-index: 2;
  width: 100%;
}
#box1 .mint-tab-item-label {
  font-size: 0.27rem;
  position: relative;
  top: -0.05rem;
}
.lunboPic {
  width: 100%;
}
.titleone .title {
  font-size: 0.3rem;
  color: #333;
  border-left: 0.1rem solid #ef1818;
  padding-left: 0.1rem;
  font-weight: 500;
}
.recommend {
  height: 2.58rem;
  padding: 0 0.2rem;
  padding-bottom: 0.2rem;
  background-color: #fff;
}
.recommend img {
  width: 100%;
  height: 2.58rem;
}
#box1 .mint-swipe-indicators {
  top: 2.2rem;
}
.titleone {
  padding: 0.1rem 0.2rem;
  background-color: #fff;
  margin-top: 0.2rem;
}
/*导航*/
.nav {
  font-size: 0.26rem;
}



