.liwu_btn div {
  width: 4.5rem;
  height: 0.8rem;
  background: #fe5875;
  color: white;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.15rem;
  font-size: 0.26rem;
  margin-bottom: 0.3rem;
}
.liwu_add {
  width: auto;
  margin: auto 0;
  height: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.24rem;
  color: #333;
}
.liwu_add span {
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  text-align: center;
}
.liwu_add .jia {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 0 0.1rem 0.1rem 0;
}
.liwu_add .jian {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 0.1rem 0 0 0.1rem;
}
.liwu_add .num {
  border: 1px solid #ccc;
  border-width: 1px 0;
}



