
.problemDetails {
  font-size: 0.205rem;
  color: #333;
  background-color: #fff;
}
.problemDetails .kefu {
  width: 4.437rem;
  position: fixed;
  z-index: 11;
  top: 1.3rem;
  left: calc(50% - 2.218rem);
}
.problemDetails .bg {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  z-index: 11;
  top: 0;
}
.problemDetails .title1 {
  background: #fff;
  font-size: 0.256rem;
  padding-left: 0.17rem;
  border-left: 3px solid #5e96d0;
  margin-left: 0.17rem;
}
.problemDetails .pList {
  background-color: #fff;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem 0.25rem;
  position: relative;
  border-bottom: 1px solid #eee;
}
.problemDetails .pList .name1 .img1 {
  width: 0.409rem;
  height: 0.409rem;
  border-radius: 50%;
  background-color: #eee;
  margin: 0.17rem 0;
}
.problemDetails .pList .name1 span {
  display: inline-block;
  height: 0.749rem;
  vertical-align: top;
  line-height: 0.749rem;
  color: #999;
}
.problemDetails .pList .problem1 {
  font-size: 0.222rem;
  margin-bottom: 0.17rem;
}
.problemDetails .pList .yuyin1 {
  width: 3.41rem;
  height: 0.682rem;
  border-radius: 0.341rem;
  position: relative;
}
.problemDetails .pList .yuyin1 span {
  color: #fff;
  font-size: 0.239rem;
  display: inline-block;
  vertical-align: top;
  line-height: 0.682rem;
  position: absolute;
  left: 1.2rem;
}
.problemDetails .pList .yuyin1 img {
  width: 3.5rem;
  position: absolute;
}
.problemDetails .pList .amount1 {
  position: absolute;
  bottom: 0.4rem;
  right: 0.17rem;
  color: #999;
}
.problemDetails .pList .pic {
  background-color: #fff;
  height: auto;
}
.problemDetails .pList .pic img:nth-child(2) {
  margin-left: 0.2rem;
}
.problemDetails .ul1 {
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  background-color: #fff;
  overflow: auto;
  border-bottom: 1px solid #eee;
  padding-bottom: 0.34rem;
}
.problemDetails .ul1 li {
  float: left;
}
.problemDetails .ul1 li:nth-child(1) img {
  width: 1.024rem;
  height: 1.024rem;
  border-radius: 50%;
}
.problemDetails .ul1 li:nth-child(2) {
  width: calc(100% - 1.024rem - 0.17rem);
  margin-left: 0.17rem;
}
.problemDetails .ul1 li:nth-child(2) .name2 {
  font-size: 0.256rem;
  margin-bottom: 0.05rem;
}
.problemDetails .ul1 li:nth-child(2) .jieshao {
  font-size: 0.239rem;
  margin-bottom: 0.05rem;
}
.problemDetails .ul1 li:nth-child(2) .shuoming {
  font-size: 0.222rem;
  color: #999;
}
.problemDetails .ul1 li:nth-child(2) .shuoming2 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.problemDetails .ul1 li:nth-child(2) .more1 {
  float: right;
  margin-top: 0.15rem;
  color: #5e96d0;
}
.problemDetails .course1 {
  overflow: auto;
  width: calc(100% - 0.34rem);
  padding: 0.256rem 0.17rem;
  border-bottom: 1px solid #eee;
}
.problemDetails .course1 .ul2 li {
  float: left;
}
.problemDetails .course1 .ul2 .img2 {
  width: 1.28rem;
  height: 1.28rem;
}
.problemDetails .course1 .ul2 li:nth-child(2) {
  width: calc(100% - 1.28rem - 0.17rem);
  margin-left: 0.17rem;
  position: relative;
  height: 1.28rem;
}
.problemDetails .course1 .ul2 .coursename {
  height: 1.1rem;
  margin-top: 0.09rem;
  position: relative;
}
.problemDetails .course1 .ul2 .coursename .down1 {
  position: absolute;
  bottom: 0;
  color: #fe5875;
  font-size: 0.222rem;
}
.problemDetails .course1 .ul2 .coursename .two {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 1.365rem;
  height: 0.5rem;
  background-color: #fe5875;
  border-radius: 0.25rem;
  text-align: center;
  line-height: 0.5rem;
  color: #fff;
  font-size: 0.222rem;
}
.problemDetails .ask1 {
  height: 0.768rem;
  width: 100%;
  line-height: 0.768rem;
  text-align: center;
  background-color: #fe5875;
  color: #fff;
  font-size: 0.256rem;
  position: fixed;
  bottom: 0;
}
.problemDetails .fast {
  width: 0.768rem;
  position: fixed;
  bottom: 0.938rem;
  right: 0.17rem;
}
.problemDetails .fast .list1,
.problemDetails .fast .list2 {
  width: 0.768rem;
  height: 0.768rem;
  background-color: rgba(0, 0, 0, 0.6);
}
.problemDetails .fast .list1 i,
.problemDetails .fast .list2 i {
  color: #fff;
  margin-left: 0.13rem;
  position: relative;
  top: 0.12rem;
  font-size: 0.5rem;
}
.problemDetails .fast .list1 {
  border-radius: 0.384rem 0.384rem 0 0;
}
.problemDetails .fast .list2 {
  border-radius: 0 0 0.384rem 0.384rem;
}



