
.codePc {
  font-size: 12px;
  width: 100%;
  height: 100%;
  background-color: #333333;
  text-align: center;
  color: #fff;
}
.codePc .datail {
  font-size: 16px;
  margin-bottom: 24px;
  padding-top: 15%;
}
.codePc .img1 {
  width: 235px;
  height: 235px;
  margin: 0 auto;
  margin-bottom: 30px;
}
.codePc .img1 img {
  width: 235px;
  height: 235px;
}
.codePc .wrap {
  float: left;
  /* 自适应内容宽度 */
  position: relative;
  left: 50%;
}
.codePc .inner {
  padding: 15px 15px;
  position: relative;
  left: -50%;
  font-size: 14px;
  border: 1px solid #666;
  border-radius: 50px;
  background-color: #202020;
}



