
#box1 {
  height: 100%;
}
#box1 .header {
  height: 1.3rem;
  width: 100%;
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  font-size: 0.26rem;
  color: #666;
}
#box1 .header .middle {
  display: table;
  height: 100%;
  width: 25%;
}
#box1 .header .middle .tablecell {
  display: table-cell;
  vertical-align: middle;
}
#box1 .header .middle .tablecell .ii {
  width: 0.7rem;
  height: 0.7rem;
  margin: 0 auto;
  border-radius: 50%;
}
#box1 .header .middle .tablecell .ii i {
  font-size: 0.45rem;
  position: relative;
  top: 0.1rem;
  left: 0.125rem;
}
#box1 .header .middle .tablecell .name {
  text-align: center;
  margin-top: 0.05rem;
  font-size: 0.205rem;
}
#box1 .wenzahng {
  height: 4.15rem;
  width: 6rem;
  margin: 0 auto;
  background-color: #fff;
  margin-top: 0.2rem;
  border-radius: 0.2rem;
  font-size: 0.28rem;
  font-weight: bold;
  -webkit-box-shadow: 0px 0px 6px #ccc;
          box-shadow: 0px 0px 6px #ccc;
}
#box1 .wenzahng .tou {
  padding-top: 0.2rem;
  margin-left: 0.2rem;
}
#box1 .wenzahng .tou span:nth-child(2) {
  float: right;
  color: #999;
  font-size: 0.25rem;
}
#box1 .wenzahng .tou span:nth-child(2) i {
  font-size: 0.25rem;
  margin-right: 0.2rem;
}
#box1 .wenzahng .tou .yi {
  margin-top: 0.4rem;
}
#box1 .wenzahng .tou .yi img {
  height: 1.5rem;
  width: 2.05rem;
}
#box1 .wenzahng .tou .yi .xinxi {
  display: inline-block;
  font-weight: normal;
  color: #333;
  position: relative;
  top: -0.45rem;
  margin-left: 0.2rem;
}
#box1 .wenzahng .tou .yi .xinxi .title {
  width: 3.2rem;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
#box1 .wenzahng .tou .qita {
  font-weight: normal;
  color: #333;
  padding-top: 0.25rem;
  width: 5.5rem;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
#box1 .wenzahng .tou .qita i {
  font-size: 0.3rem;
  margin-right: 0.1rem;
  font-weight: 500;
}
#box1 .mint-swipe {
  height: 3.413rem;
}
#box1 .mint-navbar {
  background-color: #fff;
  color: #fff;
  overflow: hidden;
  height: 0.8rem;
}
#box1 .mint-navbar .mint-tab-item.is-selected {
  color: #5e96d0;
  border-bottom: 1px solid #5e96d0;
  margin-bottom: 0px;
}
#box1 .mint-navbar {
  top: 0;
  z-index: 2;
  width: 100%;
}
#box1 .mint-tab-item-label {
  font-size: 0.27rem;
  position: relative;
  top: -0.05rem;
}
.lunboPic {
  width: 100%;
}
.titleone .title {
  font-size: 0.3rem;
  border-left: 0.1rem solid #5e96d0;
  padding-left: 0.1rem;
  font-weight: 500;
}
.recommend {
  height: 2.58rem;
  padding-bottom: 0.2rem;
  background-color: #fff;
}
.recommend img {
  width: 100%;
  height: 2.58rem;
}
#box1 .mint-swipe-indicators {
  top: 2.2rem;
}
.titleone {
  padding: 0.1rem 0.2rem;
  background-color: #fff;
  margin-top: 0.2rem;
}
/*导航*/
.nav {
  font-size: 0.26rem;
}



