
.faceSearch {
  font-size: 0.205rem;
  color: #333;
}
.faceSearch .search {
  height: 0.768rem;
  width: calc(100% -0.34rem);
  padding: 0 0.17rem;
  position: relative;
  background-color: #fff;
}
.faceSearch .search i {
  position: absolute;
  top: 0.22rem;
  left: 0.35rem;
  font-size: 0.3rem;
  color: #a9a8a8;
}
.faceSearch .search input {
  height: 0.5rem;
  width: 4.69rem;
  border-radius: 0.25rem;
  background-color: #f1f2f6;
  border: none;
  outline-style: none;
  margin-top: 0.134rem;
  padding-left: 0.6rem;
}
.faceSearch .search span {
  color: #ef4949;
  font-size: 0.222rem;
  line-height: 0.768rem;
  margin-left: 0.17rem;
}
.faceSearch .history {
  width: calc(100% - 0.34rem - 0.17rem);
  padding: 0 0.17rem 0 0.34rem;
}
.faceSearch .history .title1 {
  color: #999;
  margin-top: 0.239rem;
  margin-bottom: 0.17rem;
}
.faceSearch .history .list1 .one1 {
  padding: 0 0.25rem;
  height: 0.5rem;
  margin-bottom: 0.17rem;
  background-color: #a89e9e;
  display: inline-block;
  border-radius: 0.25rem;
  text-align: center;
  line-height: 0.5rem;
  color: #fff;
  font-size: 0.222rem;
  margin-right: 0.1rem;
}
.faceSearch .operating {
  width: 100%;
  height: 0.768rem;
  overflow: auto;
  position: fixed;
  bottom: 0;
}
.faceSearch .operating li {
  float: left;
  height: 0.768rem;
  font-size: 0.239rem;
}
.faceSearch .operating li:nth-child(1) {
  width: 2.133rem;
  background-color: #fff;
  color: #666;
  text-align: center;
}
.faceSearch .operating li:nth-child(1) i {
  line-height: 0.768rem;
  font-size: 0.28rem;
  color: #ff9d00;
}
.faceSearch .operating li:nth-child(2) {
  width: calc(100% - 2.133rem);
  background-color: #ef4949;
  color: #fff;
  line-height: 0.768rem;
  text-align: center;
}
.faceSearch .ul1 {
  height: calc(100vh - 0.768rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.faceSearch .alllist1 {
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  -webkit-column-count: 2;
  column-count: 2;
  margin-top: 0.17rem;
}
.faceSearch .alllist1 .list {
  margin-bottom: 0.17rem;
  display: inline-block;
  width: 2.98rem;
  background-color: #fff;
  height: 100%;
  overflow: auto;
}
.faceSearch .alllist1 .list .img1 {
  width: 100%;
}
.faceSearch .alllist1 .user1 {
  height: 0.682rem;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  position: relative;
}
.faceSearch .alllist1 .user1 .img3 {
  width: 0.72rem;
  height: 0.72rem;
  border-radius: 50%;
  position: absolute;
  top: -0.15rem;
  background-color: #fff;
}
.faceSearch .alllist1 .user1 .img3 .img2 {
  width: 0.68rem;
  height: 0.68rem;
  border-radius: 50%;
  position: absolute;
  top: 0.02rem;
  left: 0.02rem;
}
.faceSearch .alllist1 .user1 span {
  line-height: 0.682rem;
  margin-left: 0.85rem;
  width: 1.9rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  font-size: 0.222rem;
}
.faceSearch .alllist1 .detail1 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  overflow: hidden;
  color: #666;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
}
.faceSearch .alllist1 .zan1 {
  margin-right: 0.17rem;
  float: right;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  color: #ef4949;
}
.faceSearch .alllist1 .zan1 i {
  font-size: 0.3rem;
}
.faceSearch .tip {
  width: 4.266rem;
  height: 5.632rem;
  background-color: #ef4949;
  position: fixed;
  top: 0;
  border-radius: 0.128rem;
  margin-left: calc(50% - 2.0225rem);
  margin-top: 1rem;
}
.faceSearch .tip .tip1 {
  width: 4.045rem;
  height: 5.41rem;
  background-color: #ffeed5;
  border-radius: 0.128rem;
  margin-left: 0.1105rem;
  margin-top: 0.1105rem;
  position: relative;
}
.faceSearch .tip .tip1 i {
  position: absolute;
  right: -0.55rem;
  color: #fff;
  top: -0.55rem;
  font-size: 0.45rem;
  color: #999;
}
.faceSearch .tip .title1 {
  font-size: 0.256rem;
  color: #ef4949;
  text-align: center;
  margin-bottom: 0.17rem;
  padding-top: 0.17rem;
}
.faceSearch .tip .desc3 {
  width: calc(100% - 0.512rem);
  padding: 0 0.256rem;
}
.faceSearch .tip .desc3 .desc2 {
  margin-bottom: 0.13rem;
}
.faceSearch .bg {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
}



