
.shopCart {
  font-size: 0.205rem;
}
.shopCart .edit {
  width: 100%;
  height: 0.682rem;
  background-color: #fff;
  color: #999;
  border-bottom: 1px solid #eee;
  position: relative;
}
.shopCart .edit span {
  right: 0.17rem;
  position: absolute;
  line-height: 0.682rem;
  font-size: 0.23rem;
  display: none;
}
.shopCart .edit i {
