}
.shopCart .lists1 .list ul li:nth-child(1) .select {
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  border: 1px solid #ddd;
  display: inline-block;
  margin-top: calc(100%);
  margin-left: 0.17rem;
  position: relative;
}
.shopCart .lists1 .list ul li:nth-child(1) .select i {
  font-size: 0.3rem;
  font-weight: bold;
  border-radius: 50%;
  color: #fff;
}
.shopCart .lists1 .list ul li:nth-child(1) .select1 {
  background-color: #ef4949;
  border: 1px solid #ef4949;
  color: #fff;
}
.shopCart .lists1 .list ul li:nth-child(1) .select1 i {
  color: #fff;
  border-radius: 50%;
}
.shopCart .lists1 .list ul li:nth-child(1) .grayback {
  background-color: #999999;
}
.shopCart .lists1 .list ul li:nth-child(1) .graycolor {
