}
.shopCart .lists1 .list ul li:nth-child(2) {
  width: 1.43rem;
}
.shopCart .lists1 .list ul li:nth-child(2) .img1 {
  width: 1.433rem;
  height: 1.433rem;
  background-color: #eee;
  background-size: cover;
  margin-top: 0.17rem;
}
.shopCart .lists1 .list ul li:nth-child(3) {
  width: calc(100% - 3.685rem + 1.365rem);
