  width: 0.426rem;
  height: 0.426rem;
  display: inline-block;
  text-align: center;
  line-height: 0.426rem;
}
.shopCart .lists1 .list ul li:nth-child(3) .div1 .num11 {
  font-size: 0.205rem;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  width: 0.6rem;
}
.shopCart .lists1 .list ul li:nth-child(3) .div1 .jian1 {
  color: #999;
  border: 1px solid #ccc;
