  top: 0.1rem;
  margin-left: 0.1rem;
}
.shopCart .xia {
  height: 0.682rem;
  width: 100%;
  background-color: #fff;
  border-top: 1px solid #eee;
  position: fixed;
  bottom: 53px;
  overflow: auto;
}
.shopCart .xia ul {
  font-size: 0.222rem;
}
.shopCart .xia ul li {
