}
.shopCart .xia ul li:nth-child(1) .allradio input {
  position: absolute;
  top: 0.05rem;
  left: 0.05rem;
  opacity: 0;
}
.shopCart .xia ul li:nth-child(1) .allradio span {
  width: 0.2rem;
  height: 0.2rem;
