
.newGroup{
	font-size: 0.26rem;
}
.newGroup .row{
	height: 1rem;
	width: 100%;
	background-color: #fff;
	border-top: 1px solid #f1f2f6;
}
.newGroup .row:nth-child(1){
	border-top: 0;
}
.newGroup .row ul li{
	float: left;
}
.newGroup .row ul li:nth-child(1){
	line-height: 1rem;
	margin-left: 0.2rem;
}
.newGroup .row ul li:nth-child(2){
	float: right;
	line-height: 1rem;
}
.newGroup .row ul li:nth-child(2) input{
	background:#fff;
	border:0;
	text-align: right;
	position: relative;
	top: -0.06rem;
}
.newGroup .define{
	background: #3385ff;
	color: #fff;
	border: 0;
    width: 3rem;
    height: 0.8rem;
    border-radius: 1rem;
    margin-left: calc(50% - 1.5rem);
    margin-top: 0.5rem;
    outline-style: none;
}
.newGroup i{
	color: #e0e0e0;
	margin-right: 0.1rem;
}



