
.audit{
		height: 100%;
		background: #fff;
		width: 100%;
}
.audit .top{
		height: 3.5rem;
	    width: 100%;
	    background: -webkit-gradient(linear, left top, right bottom, from(#6ac9f6) , to(#3385ff));
	    background: linear-gradient(to bottom right, #6ac9f6 , #3385ff);
}
.audit .top1{
		height: 3.5rem;
	    width: 100%;
	    background: -webkit-gradient(linear, left top, right bottom, from(#a5e571) , to(#58af47));
	    background: linear-gradient(to bottom right, #a5e571 , #58af47);
}
.audit .top2{
		height: 3.5rem;
	    width: 100%;
	    background: -webkit-gradient(linear, left top, right bottom, from(#ff8357) , to(#ff3639));
	    background: linear-gradient(to bottom right, #ff8357 , #ff3639);
}
.audit .round{
		width: 2.4rem;
	    height: 2.4rem;
	    border: 0.1rem solid #fff;
	    border-radius: 2.4rem;
	    margin-left: calc(50% - 1.3rem);
    	top: 0.5rem;
	    position: absolute;
}
.audit #progress{ 
		stroke-dasharray: 110px;
		stroke-dashoffset: 100px;
		-webkit-animation: progressAnimation 350ms linear infinite;
		        animation: progressAnimation 350ms linear infinite;
}
.audit svg{
		margin-left: calc(50% - 1.3rem);
		margin-top: 0.5rem;
}
@-webkit-keyframes progressAnimation{
from { stroke-dashoffset: 100px;
}
to { stroke-dashoffset: 300px;
}
}
@keyframes progressAnimation{
from { stroke-dashoffset: 100px;
}
to { stroke-dashoffset: 300px;
}
}
.audit .status{
		position: absolute;
		font-size: 0.5rem;
    	color: #fff;
    	top: 1.5rem;
    	left: calc(50% - 0.99rem);
}
.audit .content{
		font-size: 0.3rem;
		text-align: center;
		margin-top: 1rem;
}
.audit .tip{
		color: #999;
		text-align: center;
		margin-top: 0.2rem;
		font-size: 0.28rem;
}
.audit .refresh{
		background: #3385ff;
		color: #fff;
		border:0;
		width: 3rem;
		height: 0.8rem;
		border-radius: 0.8rem;
		font-size: 0.26rem;
		margin-left: calc(50% - 1.5rem);
		position: absolute;
		bottom: 0.5rem;
		outline-style: none;
}
.audit .liveBtn1{
		width: 3rem;
		height: 0.7rem;
		font-size: 0.27rem;
		background: #fff;
		text-align: center;
		border:1px solid #999;
		line-height: 0.7rem;
		color: #999;
		margin-left: calc(50% - 1.5rem);
		margin-top: 0.5rem;
}



