
.tutorList {
  font-size: 0.205rem;
  color: #333;
}
.tutorList .ul3,
.tutorList .ul4 {
  height: calc(100vh - 0.768rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.tutorList .pList {
  background-color: #fff;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem 0.25rem;
  position: relative;
  margin-bottom: 0.17rem;
}
.tutorList .pList .name1 .img1 {
  width: 0.409rem;
  height: 0.409rem;
  border-radius: 50%;
  background-color: #eee;
  margin: 0.17rem 0;
}
.tutorList .pList .name1 span {
  display: inline-block;
  height: 0.749rem;
  vertical-align: top;
  line-height: 0.749rem;
  color: #999;
}
.tutorList .pList .problem1 {
  font-size: 0.222rem;
  margin-bottom: 0.17rem;
}
.tutorList .pList .yuyin1 {
  width: 3.41rem;
  height: 0.682rem;
  border-radius: 0.341rem;
  position: relative;
}
.tutorList .pList .yuyin1 span {
  color: #fff;
  font-size: 0.239rem;
  display: inline-block;
  vertical-align: top;
  line-height: 0.682rem;
  position: absolute;
  left: 1.1rem;
}
.tutorList .pList .yuyin1 img {
  width: 3.5rem;
  position: absolute;
}
.tutorList .pList .amount1 {
  position: absolute;
  bottom: 0.4rem;
  right: 0.17rem;
  color: #999;
}
.tutorList .tutorList1 {
  margin-bottom: 0.17rem;
}
.tutorList .tutorList1 .ul1 {
  background-color: #fff;
  height: 1.834rem;
}
.tutorList .tutorList1 .ul1 li {
  float: left;
}
.tutorList .tutorList1 .ul1 li:nth-child(1) {
  width: 1.365rem;
  position: relative;
  padding-top: 0.34rem;
}
.tutorList .tutorList1 .ul1 li:nth-child(1) img {
  width: 1.024rem;
  height: 1.024rem;
  border-radius: 50%;
  margin-left: 0.17rem;
}
.tutorList .tutorList1 .ul1 li:nth-child(1) .attention {
  width: 0.853rem;
  height: 0.384rem;
  border-radius: 0.192rem;
  border: 1px solid #5e96d0;
  color: #5e96d0;
  text-align: center;
  line-height: 0.384rem;
  position: absolute;
  bottom: -0.17rem;
  left: 0.255rem;
  background-color: #fff;
}
.tutorList .tutorList1 .ul1 li:nth-child(1) .attentioned {
  width: 0.853rem;
  height: 0.384rem;
  border-radius: 0.192rem;
  border: 1px solid #5e96d0;
  color: #fff;
  text-align: center;
  line-height: 0.384rem;
  position: absolute;
  bottom: -0.17rem;
  left: 0.255rem;
  background-color: #5e96d0;
}
.tutorList .tutorList1 .ul1 li:nth-child(2) {
  width: calc(100% - 1.365rem - 1.109rem - 0.17rem - 0.2rem);
  padding-right: 0.2rem;
  display: table;
}
.tutorList .tutorList1 .ul1 li:nth-child(2) .all1 {
  height: 1.834rem;
  display: table-cell;
  vertical-align: middle;
}
.tutorList .tutorList1 .ul1 li:nth-child(2) .all1 .name2 {
  font-size: 0.239rem;
  margin-bottom: 0.03rem;
}
.tutorList .tutorList1 .ul1 li:nth-child(2) .all1 .desc1 {
  color: #999;
}
.tutorList .tutorList1 .ul1 li:nth-child(3) {
  width: 1.109rem;
  height: 0.5rem;
  border: 1px solid #5e96d0;
  border-radius: 0.085rem;
  text-align: center;
  line-height: 0.5rem;
  color: #5e96d0;
  margin-top: 0.667rem;
}
.tutorList .mint-navbar {
  height: 0.768rem;
  border-bottom: 1px solid #ddd;
}
.tutorList .mint-tab-item-label {
  line-height: 0;
}
.tutorList .mint-navbar .mint-tab-item {
  color: #666;
}
.tutorList .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 1px solid #5e96d0;
  color: #5e96d0;
  margin-bottom: -1px;
}



