
.reply {
  font-size: 0.26rem;
  background-color: #fff;
  height: 100%;
  /*-webkit-user-select:none;*/
}
.reply .voice {
  height: 0.7rem;
  width: 4rem;
  display: inline-block;
  border-radius: 0.8rem;
  margin-top: 0.1rem;
  position: relative;
  margin-left: 0.3rem;
}
.reply .voice i {
  position: absolute;
  color: #fff;
  line-height: 0.8rem;
  left: 0.2rem;
}
.reply .voice span {
  line-height: 0.7rem;
  color: #fff;
  font-size: 0.26rem;
  display: inline-block;
  position: absolute;
  padding-left: 1.2rem;
  width: 2.5rem;
}
.reply .voice img {
  width: 3.6rem;
  position: absolute;
}
.reply .operating1 {
  position: absolute;
  bottom: 0;
}
.reply .pic {
  background-color: #fff;
  height: auto;
  margin-left: 0.1rem;
}
.reply .pic img {
  margin-left: 0.2rem;
}
.reply .operating {
  text-align: center;
  width: 100%;
  background-color: #fff;
  height: 2.4rem;
  margin-top: 0.5rem;
}
.reply .operating ul li {
  float: left;
  width: 33.3333%;
}
.reply .operating .record {
  height: 1.72rem;
  width: 1.72rem;
  border-radius: 1.8rem;
  display: inline-block;
  background-color: #1c252c;
}
.reply .operating .record2 {
  height: 1.72rem;
  width: 1.72rem;
  border-radius: 1.8rem;
  display: inline-block;
  background-color: #F44336;
}
.reply .operating .record2 i,
.reply .operating .record i {
  color: #fff;
  line-height: 1.72rem;
  font-size: 1rem;
}
.reply .operating ul li:nth-child(1),
.reply .operating ul li:nth-child(3) {
  line-height: 1.72rem;
}
.reply .operating .again,
.reply .operating .set {
  height: 1rem;
  width: 1rem;
  border-radius: 1rem;
  display: inline-block;
  background-color: #d6d6d6;
  text-align: center;
  line-height: 1rem;
  color: #fff;
}
.reply .operating .again1 {
  height: 1rem;
  width: 1rem;
  border-radius: 1rem;
  display: inline-block;
  background-color: #F44336;
  text-align: center;
  line-height: 1rem;
  color: #fff;
}
.reply .problem1 {
  background-color: #fff;
  font-size: 0.28rem;
  padding: 0 0.1rem 0.1rem 0.3rem;
  color: #333;
}
.reply .time {
  background-color: #fff;
  padding: 0.1rem 0.3rem;
  color: #999;
}
.reply .xiangqing {
  height: 0.6rem;
  width: 100%;
  background-color: #fff;
  font-size: 0.26rem;
  padding: 0.2rem 0 0.1rem 0;
  color: #333;
}
.reply .xiangqing ul li {
  float: left;
}
.reply .xiangqing ul li:nth-child(1) {
  margin-left: 0.3rem;
}
.reply .xiangqing ul li:nth-child(2) {
  line-height: 0.6rem;
  margin-left: 0.2rem;
}
.reply .xiangqing ul li:nth-child(3) {
  float: right;
  margin-right: 0.3rem;
  line-height: 0.6rem;
  color: red;
  font-size: 0.28rem;
}
.reply .xiangqing img {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 0.6rem;
}
.reply .xiangqing .name {
  font-size: 0.26rem;
}



