
.orderDetails {
  color: #000;
  font-size: 0.205rem;
}
.orderDetails .status {
  padding: 0.25rem 0.34rem;
  width: calc(100% - 0.68rem);
  background-color: #ef4949;
  color: #fff;
  font-size: 0.222rem;
}
.orderDetails .status i {
  font-size: 0.3rem;
  margin-right: 0.08rem;
}
.orderDetails .status .left span {
  position: relative;
  top: -0.02rem;
}
.orderDetails .status .right {
  float: right;
}
.orderDetails .info {
  width: 100%;
  background-color: #fff;
  height: auto;
  padding-top: 0.25rem;
  position: relative;
  margin-bottom: 0.17rem;
}
.orderDetails .info .name1 {
  width: calc(100% - 1.4rem);
  padding-left: 0.7rem;
  margin-bottom: 0.17rem;
}
.orderDetails .info .name1 .phone {
  float: right;
}
.orderDetails .info .address {
  width: calc(100% - 0.68rem);
  padding-left: 0.34rem;
  color: #666;
  padding-bottom: 0.25rem;
}
.orderDetails .info .address i {
  font-size: 0.25rem;
  margin-right: 0.05rem;
}
.orderDetails .info .address span {
  width: 5.1rem;
  display: inline-block;
}
.orderDetails .orderlist {
  width: 100%;
  height: 1.792rem;
  background-color: #fff;
}
.orderDetails .orderlist .ul1 li {
  float: left;
}
.orderDetails .orderlist .ul1 li:nth-child(1) {
  width: 1.9rem;
}
.orderDetails .orderlist .ul1 li:nth-child(1) .img1 {
  width: 1.433rem;
  height: 1.433rem;
  background-color: #eee;
  margin-left: 0.25rem;
  margin-top: 0.17rem;
  background-size: cover;
}
.orderDetails .orderlist .ul1 li:nth-child(2) {
  width: calc(100% - 2.15rem);
  height: 1.792rem;
  position: relative;
}
.orderDetails .orderlist .ul1 li:nth-child(2) .name2 {
  font-size: 0.222rem;
  margin-top: 0.17rem;
  max-height: 0.83rem;
  overflow: hidden;
}
.orderDetails .orderlist .ul1 li:nth-child(2) .sku1 {
  margin-top: 0.06rem;
  color: #999;
}
.orderDetails .orderlist .ul1 li:nth-child(2) .price1 {
  font-size: 0.222rem;
  position: absolute;
  bottom: 0.17rem;
  width: 100%;
}
.orderDetails .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(1) {
  color: #ef4949;
}
.orderDetails .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(2) {
  color: #999;
  text-decoration: line-through;
  margin-left: 0.1rem;
}
.orderDetails .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(3) {
  color: #999;
  float: right;
}
.orderDetails .everyPrice {
  background-color: #fff;
  color: #999;
}
.orderDetails .everyPrice .list1 {
  width: calc(100% - 0.34rem);
  padding: 0.08rem 0.17rem;
}
.orderDetails .everyPrice .list1 span:nth-child(2) {
  float: right;
}
.orderDetails .allPrice1 {
  background-color: #fff;
  font-size: 0.222rem;
}
.orderDetails .allPrice1 .list2 {
  width: calc(100% - 0.34rem);
  margin: 0 0.17rem;
  padding: 0.08rem 0;
  letter-spacing: 1px;
}
.orderDetails .allPrice1 .list2 span:nth-child(2) {
  float: right;
}
.orderDetails .orderCode {
  padding: 0.17rem 0.17rem 0.2rem;
  margin-top: 0.17rem;
  width: calc(100% - 0.34rem);
  margin-bottom: 0.75rem;
  background-color: #fff;
  color: #999;
}
.orderDetails .orderCode .list3 {
  margin-bottom: 2px;
}
.orderDetails .caozuo1 {
  width: calc(100% - 0.34rem);
  padding: 0.25rem 0.17rem;
  background-color: #fff;
  border-top: 1px solid #ddd;
  position: fixed;
  z-index: 11;
  bottom: 0;
  /*ul{*/
  /*li{*/
  /*float: right;*/
  /*}*/
  /*.btn1{*/
  /*text-align: center;*/
  /*border-radius: 0.192rem;*/
  /*border: 1px solid #999;*/
  /*padding: 0.06rem 0.15rem;*/
  /*margin-left: 0.17rem;*/
  /*}*/
  /*}*/
}
.orderDetails .caozuo1 span {
  height: 0.384rem;
  width: 1.143rem;
  border-radius: 0.192rem;
  border: 1px solid #999;
  padding: 0.06rem 0.15rem;
  margin-left: 0.17rem;
}
.orderDetails .caozuo1 span:last-of-type {
  color: #ef4949;
  border-color: #ef4949;
}



