
#goodsrelation .relation_bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
#goodsrelation .relation_box {
  position: fixed;
  right: 0;
  top: 0;
  width: 80%;
  height: 100%;
  background-color: white;
  z-index: 2;
}
#goodsrelation .relation_title {
  height: 0.8rem;
  line-height: 0.8rem;
  padding-left: 0.2rem;
  font-size: 0.3rem;
  color: #333333;
}
#goodsrelation .relation_content {
  padding: 0 0.15rem;
  height: 100%;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
#goodsrelation .relation_item {
  background-color: #f2f2f2;
  margin: 0.15rem 0;
}
#goodsrelation .relation_item:after {
  display: block;
  content: '';
  clear: both;
}
#goodsrelation .relation_item_pic {
  float: left;
  width: 30%;
  padding: 0.15rem;
}
#goodsrelation .relation_item_right {
  float: right;
  width: 63%;
  padding: 0.15rem 0;
  height: 1.5rem;
  position: relative;
}
#goodsrelation .relation_item_title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  color: #333333;
  font-size: 0.26rem;
  /*height: 0.75rem;*/
}
#goodsrelation .relation_item_price {
  height: 0.75rem;
  position: absolute;
  bottom: 0;
  width: 100%;
  line-height: 0.75rem;
  /*position: relative;*/
}
#goodsrelation .relation_item_price:after {
  display: block;
  content: "";
  clear: both;
}
#goodsrelation .relation_item_price > div:first-child {
  font-size: 0.26rem;
  color: #fe5875;
  float: left;
}
#goodsrelation .relation_item_price > div:last-child {
  width: 1.4rem;
  height: 0.5rem;
  line-height: 0.5rem;
  text-align: center;
  background-color: #fe5875;
  font-size: 0.25rem;
  color: white;
  border-radius: 0.3rem;
  float: right;
  margin-right: 0.2rem;
}
#goodsrelation .relation_item_pic > img {
  width: 1.5rem;
  height: 1.5rem;
}
#goodsrelation .relation_more {
  width: 100%;
  text-align: center;
  font-size: 0.24rem;
  color: #cccccc;
  height: 1.4rem;
}
#goodsrelation .relation_btn {
  position: absolute;
  width: 10%;
  height: 1.2rem;
  left: -10%;
  top: 50%;
  line-height: 1.2rem;
  border-top-left-radius: 0.15rem;
  border-bottom-left-radius: 0.15rem;
  background-color: white;
}
#goodsrelation .relation_btn > .iconfont {
  font-size: 0.5rem;
  color: #cccccc;
}
#goodsrelation .move-enter-active,
#goodsrelation .move-leave-active {
  -webkit-transition: all 0.6s cubic-bezier(0, 0, 1, 1);
  transition: all 0.6s cubic-bezier(0, 0, 1, 1);
  -webkit-transform: translateX(-1%);
          transform: translateX(-1%);
}
#goodsrelation .move-enter,
#goodsrelation .move-leave-to {
  -webkit-transition: all 0.6s cubic-bezier(0, 0, 1, 1);
  transition: all 0.6s cubic-bezier(0, 0, 1, 1);
  transform: translateX(100%);
  -moz-transform: translateX(100%);
  -ms-transform: translateX(100%);
  -o-transform: translateX(100%);
  -webkit-transform: translateX(100%);
}



