
.askQuestions {
  font-size: 0.205rem;
  color: #333;
}
.askQuestions .askText {
  width: calc(94.6%);
  height: 1.96rem;
  border: 0;
  font-size: 0.26rem;
  color: #333;
  padding: 0.3rem 0.2rem;
  outline-style: none;
  letter-spacing: 1px;
}
.askQuestions .uploadPic {
  height: 0.6rem;
  width: 100%;
  background-color: #fff;
  position: relative;
  top: -0.2rem;
}
.askQuestions .uploadPic i {
  font-size: 0.4rem;
  color: #666;
  margin-right: 0.2rem;
  float: right;
}
.askQuestions .uploadPic img {
  height: 0.4rem;
  float: right;
  margin-right: 0.2rem;
}
.askQuestions .uploadPic input {
  display: none;
  opacity: 0;
}
.askQuestions .tiwen {
  height: 0.88rem;
  padding-top: 0.15rem;
  font-size: 0.26rem;
}
.askQuestions .tiwen li {
  float: left;
  text-align: center;
  width: 100%;
}
.askQuestions .tiwen li span {
  width: 6rem;
  height: 0.69rem;
  display: inline-block;
  line-height: 0.69rem;
  color: #fff;
  border-radius: 0.1rem;
}
.askQuestions .questatus {
  height: 0.8rem;
  background-color: #fff;
  line-height: 0.8rem;
  font-size: 0.26rem;
  color: #333;
  padding-left: 0.15rem;
  position: relative;
  border-bottom: 1px solid #f1f2f6;
}
.askQuestions .questatus .gou {
  width: 0.35rem;
  height: 0.35rem;
  border-radius: 100%;
  border: 1px solid #ddd;
  display: inline-block;
  float: right;
  position: absolute;
  top: 0.2rem;
  right: 0.15rem;
}
.askQuestions .questatus .xuan {
  width: 0.35rem;
  height: 0.35rem;
  border-radius: 100%;
  background-color: #5e96d0;
  display: inline-block;
  position: absolute;
}
.askQuestions .questatus .xuan i {
  position: relative;
  top: -0.2rem;
  color: #fff;
  font-size: 0.33rem;
}
.askQuestions .questatus input {
  float: right;
  display: inline-block;
  position: absolute;
  top: 0.28rem;
  right: 0.2rem;
  z-index: 1;
  opacity: 0;
}
.askQuestions .questatus i.icon-shangla,
.askQuestions .questatus i.icon-xiala {
  float: right;
  margin-right: 0.12rem;
  color: #ddd;
}
.askQuestions .queTip {
  background-color: #fff;
  height: 1.7rem;
  overflow: hidden;
  font-size: 0.22rem;
  padding: 0rem 0.15rem;
}
.askQuestions .queTip div {
  line-height: 0.4rem;
  color: #666;
}



