
.detailquestions .answerUp {
  background-color: #fff;
  margin-bottom: 0.15rem;
  height: auto;
}
.detailquestions .answer .listlenCount {
  position: absolute;
  right: 0;
  line-height: 1.2rem;
  color: #999;
}
.detailquestions .answer .personal ul {
  width: 100%;
}
.detailquestions .answer .personal ul li {
  float: left;
}
.detailquestions .answer .personal ul li:nth-child(2) {
  line-height: 0.6rem;
  margin-left: 0.2rem;
  color: #999;
}
.detailquestions .answer .personal {
  display: block;
  height: 0.66rem;
  padding-top: 0.2rem;
}
.detailquestions .answer .personal .time {
  float: right;
  line-height: 0.66rem;
}
.detailquestions .answer .name {
  font-size: 0.24rem;
}
.detailquestions .answer .problem {
  font-size: 0.28rem;
  color: #333;
  margin-top: 0.1rem;
}
.detailquestions .answer .voice {
  height: 0.7rem;
  width: 4rem;
  display: inline-block;
  border-radius: 0.8rem;
  margin-top: 0.2rem;
  position: relative;
}
.detailquestions .answer .voice i {
  position: absolute;
  color: #fff;
  line-height: 0.8rem;
  left: 0.2rem;
}
.detailquestions .answer .voice span {
  line-height: 0.7rem;
  color: #fff;
  font-size: 0.26rem;
  display: inline-block;
  position: absolute;
  left: 1.1rem;
}
.detailquestions .answer .personal img {
  height: 0.6rem;
  width: 0.6rem;
  border-radius: 0.6rem;
}
.detailquestions .answer {
  width: 100%;
  padding-bottom: 0.2rem;
}



