}
.stduy .mint-tab-container-item .collect .tutorList .ul1 li:nth-child(3) {
  width: 1.109rem;
  height: 0.5rem;
  border: 1px solid #5e96d0;
  border-radius: 0.085rem;
  text-align: center;
  line-height: 0.5rem;
  color: #5e96d0;
  margin-top: 0.667rem;
}



