
.successPay {
  font-size: 0.27rem;
}
.successTitle {
  height: 0.8rem;
  width: 100%;
  background-color: #3d3c41;
  text-align: center;
  color: #fff;
  line-height: 0.8rem;
}
.successPay img {
  height: 1.8rem;
  margin-left: calc(50% - 0.9rem);
  margin-top: 1rem;
}
.successPay .tip {
  font-weight: 500;
  text-align: center;
  font-size: 0.55rem;
  margin-top: 0rem;
}
.successPay .btn {
  height: 0.8rem;
  width: 6rem;
  background: #00c800;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.4rem;
  color: #fff;
  margin: 0 auto;
  margin-top: 1.8rem;
}



