
.payWayall {
  font-size: 0.27rem;
  height: 100%;
}
.payWayall .payTitle {
  height: 0.8rem;
  width: 100%;
  text-align: center;
  line-height: 0.8rem;
  background-color: #fff;
  margin-bottom: 0.2rem;
  border-bottom: 1px solid #e1e1e1;
}
.payWayall .payWay {
  background-color: #fff;
  padding-left: 0.17rem;
  height: 0.55rem;
  line-height: 0.55rem;
  margin-top: 0.2rem;
  border-bottom: 1px solid #e1e1e1;
}
.payWayall .payWay1 {
  height: 0.8rem;
  background-color: #fff;
  border-top: 1px solid #e1e1e1;
}
.payWayall .payWay1 ul li {
  float: left;
}
.payWayall .payWay1 ul li:nth-child(1) {
  padding-left: 0.17rem;
  line-height: 0.8rem;
}
.payWayall .payWay1 ul li:nth-child(1) img {
  height: 0.4rem;
  position: relative;
  top: 0.1rem;
}
.payWayall .payWay1 ul li:nth-child(1) span {
  display: inline-block;
  margin-left: 0.2rem;
}
.payWayall .payWay1 ul li:nth-child(2) {
  float: right;
}
.payWayall .payWay1 .radio i {
  font-size: 0.28rem;
}
.payWayall .payWay1 ul li:nth-child(2) .radio {
  width: 0.4rem;
  height: 0.4rem;
  background-color: #fff;
  border: 1px solid #e1e1e1;
  display: inline-block;
  border-radius: 0.4rem;
  margin-top: 0.2rem;
  margin-right: 0.17rem;
  text-align: center;
  line-height: 0.4rem;
  color: #fff;
}
.payWayall .btnPay {
  height: 0.8rem;
  background: #3385ff;
  color: #fff;
  width: 100%;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.3rem;
  position: fixed;
  bottom: 0px;
}
.payWayall .endTime {
  text-align: center;
  font-size: 0.26rem;
  color: #666;
  margin-top: 0.3rem;
}



