
#customInfo {
  font-size: 0.24rem;
}
#customInfo .userinfo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  height: 0.77rem;
  background-color: #FFF8E0;
}
#customInfo .userinfo-left img {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  display: inline-block;
  margin-top: 0.085rem;
  margin-left: 0.17rem;
  margin-right: 0.05rem;
}
#customInfo .userinfo-left-name {
  display: inline-block;
  color: #333;
  vertical-align: top;
  line-height: 0.77rem;
}
#customInfo .userinfo-left button {
  width: 1.2rem;
  height: 0.36rem;
  border-radius: 0.18rem;
  color: #FF5B0A;
  text-align: center;
  border: 1px solid #FF5B0A;
  background-color: transparent;
  display: inline-block;
  vertical-align: top;
  margin-top: calc(0.385rem - 0.18rem);
  margin-left: 0.03rem;
  outline-style: none;
}
#customInfo .userinfo-left .userinfo-right {
  display: inline-block;
  vertical-align: top;
}
#customInfo .userinfo-right img {
  display: inline-block;
  width: 0.35rem;
  height: 0.35rem;
  margin-top: 0.21rem;
}
#customInfo .userinfo-right-phone {
  display: inline-block;
  vertical-align: top;
  margin-right: 0.17rem;
  margin-left: 0.02rem;
  line-height: 0.77rem;
  color: #333;
}
#customInfo .userinfo-right button {
  width: 1.2rem;
  height: 0.36rem;
  border-radius: 0.18rem;
  color: #FF5B0A;
  text-align: center;
  border: 1px solid #FF5B0A;
  background-color: transparent;
  display: inline-block;
  vertical-align: top;
  margin-top: calc(0.385rem - 0.18rem);
  margin-left: 0.03rem;
  outline-style: none;
  margin-right: 0.17rem;
}



