
.shopAddress {
  font-size: 0.24rem;
  background-color: #f9f9f9;
  height: 100vh;
}
.shopAddress_list {
  padding-bottom: 1rem;
}
.shopAddress_item {
  margin-top: 0.2rem;
  background-color: #ffffff;
}
.shopAddress_item:first-of-type {
  margin-top: 0;
}
.shopAddress_item-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 1.3rem;
  padding-left: 0.3rem;
}
.shopAddress_item-name {
  margin-bottom: 0.2rem;
  font-size: 0.26rem;
  color: #333333;
}
.shopAddress_item-name span:first-of-type {
  margin-right: 0.8rem;
}
.shopAddress_item-address {
  font-size: 0.24rem;
  color: #666666;
}
.shopAddress_item-operation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 0.7rem;
  padding: 0 0.2rem;
  border-top: 0.02rem solid #f2f2f2;
}
.shopAddress_item-owrap span {
  display: inline-block;
  /*line-height: 0.7rem;*/
}
.shopAddress_item-owrap:nth-of-type(2) {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: right;
}
.shopAddress_item-owrap:nth-of-type(2) span:nth-of-type(2) {
  margin-right: 0.2rem;
}
.shopAddress_item-owrap .icon-bianji {
  font-size: 0.34rem;
  vertical-align: -0.03rem;
}
.shopAddress_item-owrap .icon-check {
  font-size: 0.4rem;
  vertical-align: -0.04rem;
  color: #ef4949;
  border: 0.01rem solid #cccccc;
  border-radius: 100%;
}
.shopAddress_item-owrap .noCheck {
  color: transparent;
}
.shopAddress_item-owrap .icon-iconfonticonfontdelete {
  font-size: 0.32rem;
  vertical-align: 0;
}
.shopAddress_btn {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  line-height: 0.9rem;
  text-align: center;
  background-color: #ef4949;
}
.shopAddress_btn span {
  display: block;
  font-size: 0.3rem;
  color: #ffffff;
}
.shopAddress_btn .icon-jiahao {
  margin-right: 0.1rem;
  font-size: 0.3rem;
  color: #ffffff;
}



