
.sortLists {
  font-size: 0.205rem;
}
.sortLists .search {
  height: 0.768rem;
  background-color: #fff;
  width: calc(100% - 0.34rem);
  margin-left: 0.17rem;
}
.sortLists .search i {
  position: absolute;
  color: #ccc;
  top: 0.2rem;
  left: 0.1rem;
  font-size: 0.35rem;
}
.sortLists .search input {
  height: 0.426rem;
  border-radius: 0.213rem;
  -webkit-appearance: none;
  outline-style: none;
  border: 1px solid #eee;
  margin-top: 0.17rem;
  background-color: #eee;
  text-align: center;
  width: 5.5rem;
}
.sortLists .search span {
  font-size: 0.222rem;
  line-height: 0.768rem;
}
.sortLists .search div {
  display: inline-block;
}
.sortLists .lists ul li {
  float: left;
}
.sortLists .lists ul li .idLists {
  height: calc(100vh - 0.768rem - 53px);
  width: 1.706rem;
  background-color: #fff;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.sortLists .lists ul li .idLists .idlist {
  height: 0.683rem;
  width: calc(100% - 0.2rem);
  padding-left: 0.1rem;
  padding-right: 0.17rem;
  line-height: 0.683rem;
  font-size: 0.222rem;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.sortLists .lists ul li .idLists .redone {
  background-color: #eee !important;
  color: #ef4949 !important;
}
.sortLists .lists ul li .rightList {
  width: calc(100% - 0.34rem);
  margin-left: 0.17rem;
}
.sortLists .lists ul li .rightList .childTitle {
  margin: 0.17rem 0;
}
.sortLists .lists ul li .rightList .childTitle .childlists {
  background-color: #fff;
}
.sortLists .lists ul li .rightList .childTitle .childlists .ul2 {
  width: 100%;
  display: inline-block;
}
.sortLists .lists ul li .rightList .childTitle .childlists .ul2 li {
  width: 33.33333333%;
  background-color: #fff;
  text-align: center;
}
.sortLists .lists ul li .rightList .childTitle .childlists .ul2 li .img1 {
  width: 1.066rem;
  height: 1.066rem;
  background-color: #eee;
  margin: 0 auto;
  margin-top: 0.17rem;
  background-size: cover;
}
.sortLists .lists ul li .nodata {
  width: 100%;
  text-align: center;
  color: #666;
  margin-top: 50%;
}



