
.promotion {
  font-size: 0.205rem;
  color: #333;
  background-color: #333;
  width: 100%;
  height: calc(100vh - 0.34rem);
  padding-top: 0.34rem;
}
.promotion .tu {
  width: calc(100% - 50% - 2.474rem);
  padding-left: calc(50% - 2.474rem);
}
.promotion .tu img {
  width: 4.949rem;
  height: 7.697rem;
}
.promotion .tip {
  width: 100%;
  text-align: center;
  color: #fff;
  margin-top: 0.15rem;
}
.promotion .tip img {
  width: 0.2rem;
}
.promotion .bottom {
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  overflow-x: scroll;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  height: 1.536rem;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}
.promotion .bottom .one {
  display: inline-block;
  margin-right: 0.17rem;
  margin-top: 0.235rem;
  position: relative;
}
.promotion .bottom .one img:nth-child(1) {
  width: 1.066rem;
  height: 1.066rem;
}
.promotion .bottom .one img:nth-child(2) {
  position: absolute;
  right: 0.08rem;
  bottom: 0.13rem;
  color: #fe5875;
  width: 0.34rem;
  display: none;
}
.promotion .bottom .one .block1 {
  display: block !important;
}



