  font-size: 0.27rem;
  color: #333;
  border-top: 1px solid #e1e2e6;
}
.box {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-size: 0.26rem;
  height: 100%;
