.bankcard input {
  height: 0.5rem;
  border: none;
  outline-style: none;
}
.login {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 1rem;
}
.bankcard button {
  width: 4rem;
  height: 0.8rem;
  font-size: 0.26rem;
  outline-style: none;
  border: none;
  border-radius: 0.4rem;
  background-color: #3385ff;
  color: white;
}
.bankTitle {
