
.editAddress {
  height: 100vh;
  font-size: 0.24rem;
  background-color: #f9f9f9;
}
.editAddress_list {
  background-color: #ffffff;
  padding: 0 0.2rem;
}
.editAddress_item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 0.8rem;
  font-size: 0.26rem;
  color: #333333;
  border-bottom: 0.02rem solid #eeeeee;
}
.editAddress_item:last-of-type {
  border-bottom: 0;
}
.editAddress_item span {
  margin-right: 0.25rem;
}
.editAddress_item input {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0.26rem;
  color: #333333;
  border: none;
  resize: none;
}
.editAddress_item input:focus {
  outline: none;
}
.editAddress_item input:disabled {
  background-color: #ffffff;
}
.editAddress_item .icon-dayuhao1 {
  color: #cccccc;
  font-size: 0.35rem;
}
.editAddress_operation {
  display: table;
  width: 100%;
  height: 1.25rem;
  margin-top: 0.2rem;
  background-color: #ffffff;
}
.editAddress_operation-text {
  display: table-cell;
  vertical-align: middle;
  padding-left: 0.2rem;
}
.editAddress_operation-text span {
  display: block;
  font-size: 0.24rem;
  color: #cccccc;
}
.editAddress_operation-text span:first-of-type {
  margin-bottom: 0.2rem;
  font-size: 0.26rem;
  color: #333333;
}
.editAddress_operation-option {
  display: table-cell;
  width: 0.7rem;
  vertical-align: middle;
  padding-right: 0.2rem;
}
.editAddress_btn {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  line-height: 0.9rem;
  text-align: center;
  font-size: 0.3rem;
  color: #ffffff;
  background-color: #ef4949;
}
.editAddress_picker-head {
  text-align: center;
  font-size: 0.26rem;
  color: #999999;
}
.editAddress .showChose {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.51);
}
.editAddress .showChose .address {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50%;
  background-color: #ffffff;
}
.editAddress .showChose .address .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.editAddress .showChose .address .title .icon-guanbi {
  font-size: 0.3rem;
}
.editAddress .showChose .address .title:first-of-type {
  padding: 0.3rem 0.2rem 0;
  font-size: 0.26rem;
  color: #999999;
}
.editAddress .showChose .address .title:first-of-type h4 {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.editAddress .showChose .address .title:nth-of-type(2) {
  font-size: 0.24rem;
  color: #333333;
  border-bottom: 0.02rem solid #eeeeee;
}
.editAddress .showChose .address .title:nth-of-type(2) .area {
  line-height: 0.65rem;
  padding: 0 0.15rem;
}
.editAddress .showChose .address .title:nth-of-type(2) .active {
  border-bottom: 0.02rem solid #ef4949;
}
.editAddress .showChose .address .wrap {
  height: 80%;
  overflow-y: scroll;
}
.editAddress .showChose .address .wrap ul {
  padding-left: 0.2rem;
  padding-bottom: 10%;
}
.editAddress .showChose .address .wrap ul .addList {
  font-size: 0.24rem;
  color: #333333;
  margin-top: 0.3rem;
}



