
.confirmOrder {
  font-size: 0.205rem;
  width: 100%;
  font-size: 0.222rem;
  color: #333;
}
.confirmOrder .getInfo {
  width: 100%;
  height: 0.95rem;
  background-color: #fff;
  margin-bottom: 0.17rem;
  color: #666;
  font-size: 0.222rem;
}
.confirmOrder .getInfo span {
  line-height: 0.95rem;
}
.confirmOrder .getInfo i {
  position: relative;
  top: 0.05rem;
  margin-left: 0.17rem;
}
.confirmOrder .info {
  width: 100%;
  background-color: #fff;
  height: auto;
  padding-top: 0.25rem;
  position: relative;
  margin-bottom: 0.17rem;
}
.confirmOrder .info .icon-next-page {
  position: absolute;
  right: 0.17rem;
  font-size: 0.3rem;
  top: 0.5rem;
  color: #999;
}
.confirmOrder .info .name1 {
  width: calc(100% - 1.4rem);
  padding-left: 0.7rem;
  margin-bottom: 0.17rem;
}
.confirmOrder .info .name1 .phone {
  float: right;
}
.confirmOrder .info .address {
  width: calc(100% - 0.68rem);
  padding-left: 0.34rem;
  color: #666;
  padding-bottom: 0.25rem;
}
.confirmOrder .info .address i {
  font-size: 0.25rem;
  margin-right: 0.05rem;
}
.confirmOrder .info .address span {
  width: 5.1rem;
  display: inline-block;
}
.confirmOrder .orderlist {
  width: 100%;
  height: 1.792rem;
  background-color: #fff;
}
.confirmOrder .orderlist .ul1 li {
  float: left;
}
.confirmOrder .orderlist .ul1 li:nth-child(1) {
  width: 1.9rem;
}
.confirmOrder .orderlist .ul1 li:nth-child(1) .img1 {
  width: 1.433rem;
  height: 1.433rem;
  background-color: #eee;
  margin-left: 0.25rem;
  margin-top: 0.17rem;
  background-size: cover;
}
.confirmOrder .orderlist .ul1 li:nth-child(2) {
  width: calc(100% - 2.15rem);
  height: 1.792rem;
  position: relative;
}
.confirmOrder .orderlist .ul1 li:nth-child(2) .name2 {
  font-size: 0.222rem;
  margin-top: 0.17rem;
  max-height: 0.83rem;
  overflow: hidden;
}
.confirmOrder .orderlist .ul1 li:nth-child(2) .sku1 {
  margin-top: 0.06rem;
  color: #999;
}
.confirmOrder .orderlist .ul1 li:nth-child(2) .price1 {
  font-size: 0.222rem;
  position: absolute;
  bottom: 0.17rem;
  width: 100%;
}
.confirmOrder .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(1) {
  color: #ef4949;
}
.confirmOrder .orderlist .ul1 li:nth-child(2) .price1 span:nth-child(2) {
  color: #999;
  float: right;
}
.confirmOrder .msg {
  background-color: #fff;
  padding: 0.25rem 0.17rem;
  width: calc(100% - 0.34rem);
  margin-top: 0.17rem;
}
.confirmOrder .msg span {
  display: inline-block;
  vertical-align: top;
}
.confirmOrder .msg textarea {
  border: 0;
  outline-style: none;
  width: 4.8rem;
  height: 1.2rem;
  -webkit-appearance: none;
  resize: none;
  border: none;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-all;
}
.confirmOrder .wechat {
  background-color: #fff;
  padding: 0.17rem 0.17rem;
  width: calc(100% - 0.34rem);
  margin-bottom: 0.17rem;
}
.confirmOrder .wechat i {
  font-size: 0.25rem;
  color: #24af41;
  margin-right: 0.08rem;
}
.confirmOrder .wechat .radio1 {
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  border: 1px solid #eee;
  display: inline-block;
  float: right;
  position: relative;
}
.confirmOrder .wechat .radio1 .limian {
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 50%;
  background-color: #24af41;
  display: inline-block;
  margin-left: 0.05rem;
  margin-top: 0.05rem;
}
.confirmOrder .wechat .radio1 input {
  position: absolute;
  top: 0;
}
.confirmOrder .yue {
  background-color: #fff;
  height: 0.682rem;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  border-bottom: 1px solid #eee;
}
.confirmOrder .yue span:nth-child(1) {
  line-height: 0.682rem;
  font-size: 0.222rem;
}
.confirmOrder .yue span:nth-child(2) {
  display: inline-block;
  vertical-align: top;
  float: right;
  margin-top: 0.04rem;
}
.confirmOrder .yue .mint-switch-input:checked + .mint-switch-core {
  border-color: #24af41;
  background-color: #24af41;
}
.confirmOrder .yue .mint-switch {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.confirmOrder .yue1 {
  background-color: #fff;
  height: 0.682rem;
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  margin-bottom: 0.17rem;
}
.confirmOrder .yue1 input {
  float: right;
  margin-top: 0.17rem;
  outline-style: none;
  -webkit-appearance: none;
  border: 1px solid #ddd;
}
.confirmOrder .price,
.confirmOrder .yunfei,
.confirmOrder .dikou {
  height: 0.682rem;
  width: calc(100% - 0.34rem);
  background-color: #fff;
  padding-left: 0.17rem;
  padding-right: 0.17rem;
  line-height: 0.682rem;
}
.confirmOrder .price span:nth-child(2),
.confirmOrder .yunfei span:nth-child(2),
.confirmOrder .dikou span:nth-child(2) {
  float: right;
  color: #ef4949;
}
.confirmOrder .pay {
  height: 0.768rem;
  width: 100%;
  background-color: #fff;
  border-top: 1px solid #eee;
  position: fixed;
  bottom: 0;
}
.confirmOrder .pay ul {
  width: 100%;
}
.confirmOrder .pay ul li {
  float: left;
}
.confirmOrder .pay ul li:nth-child(1) {
  width: calc(100% - 2.133rem);
  line-height: 0.768rem;
  font-size: 0.239rem;
}
.confirmOrder .pay ul li:nth-child(1) span:nth-child(1) {
  margin-left: 0.17rem;
}
.confirmOrder .pay ul li:nth-child(1) span:nth-child(2) {
  color: #ef4949;
}
.confirmOrder .pay ul li:nth-child(2) {
  width: 2.133rem;
  text-align: center;
  line-height: 0.768rem;
  background-color: #ef4949;
  color: #fff;
  font-size: 0.256rem;
}



