
#customPage {
  overflow: hidden;
  /*padding-bottom: 1.5rem;*/
  font-size: 0.24rem;
  /*视频*/
}
#customPage .bg {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 1;
  background: rgba(0, 0, 0, 0.6);
}
#customPage .infoedit {
  width: 9rem;
  height: 9rem;
  background: #fff;
  position: fixed;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 4.1rem;
  height: 4.2rem;
  z-index: 2;
  border-radius: 0.05rem;
}
#customPage .infoedit-title {
  font-size: 0.29rem;
  font-weight: bold;
  text-align: center;
  margin-top: 0.32rem;
}
#customPage .infoedit-tip {
  font-size: 0.24rem;
  text-align: center;
  margin: 0 0.36rem;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}
#customPage .infoedit-inp1 {
  margin: 0 0.36rem;
  margin-bottom: 0.25rem;
  border-bottom: 1px solid #999;
}
#customPage .infoedit-inp1-left input {
  border: 0px;
  width: 2.8rem;
  outline-style: none;
  margin-bottom: 0.15rem;
  padding-top: 0.15rem;
  font-size: 0.23rem;
}
#customPage .infoedit-inp1-left img {
  width: 0.255rem;
  height: 0.255rem;
  margin-right: 0.1rem;
  position: relative;
  top: 0.03rem;
}
#customPage .infoedit-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.24rem;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0 0.36rem;
  margin-top: 0.3rem;
}
#customPage .infoedit-btn-left,
#customPage .infoedit-btn-right {
  width: 50%;
  text-align: center;
}
#customPage .infoedit-btn-left {
  border-right: 1px solid #eee;
}
#customPage .infoedit-btn-right {
  color: #5A8EC4;
}
#customPage .customPage_null {
  width: 100%;
  height: 100vh;
  text-align: center;
  font-size: 0.38rem;
  color: #999999;
}
#customPage .customPage_null .icon-empty {
  font-size: 1.8rem;
  color: #999999;
}
#customPage .customPage_null p {
  padding-top: 50%;
}
#customPage .customPage_video-wrap {
  padding: 0.1rem;
  background-color: transparent;
}
#customPage .customPage_video-cover {
  position: absolute;
  width: 100%;
  height: 100%;
}
#customPage .customPage_video-coverimg {
  width: 100%;
  height: 100%;
}
#customPage .customPage_video-backColor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
#customPage .customPage_video-play {
  position: absolute;
  left: 45%;
  top: 40%;
}
#customPage .customPage_video-play .icon-8shipinicon {
  font-size: 1rem;
  color: #0086b3;
}
#customPage .customPage_video-iframe {
  width: 100%;
  height: 4.5rem;
  border: 0;
}



